<template>
  <div id="x_currentlocation">
    <div>当前位置：</div>
    <div @click="function(){$router.push('/')}">服务平台 ></div>
    <div class="last_last_time_location" @click="link()">{{last_last_location}}</div>
    <div class="last_time_location" @click="link()">{{last_location}}</div>
    <div class="now_location">{{ current_where}}</div>

  </div>
</template>

<script>
export default {
  props: {
    current_where: {
      type: String,
      required: true
    },
    last_location: {
      type: String,
      required: true
    },
    last_last_location: {
      type: String,
      required: true
    }
  },
  methods: {
    link() {
      if (this.last_location == "人才>") {
        var path = "/talent";
        this.$router.push(path); //路由跳转
      } else if (this.last_location == "协会>") {
        var path = "/association";
        this.$router.push(path); //路由跳转
      } else if (this.last_location == "服务平台 >") {
        var path = "/home";
        this.$router.push(path); //路由跳转
      } else if (this.last_location == "资讯>") {
        var path = "/news";
        this.$router.push(path); //路由跳转
      }
      if (this.last_last_location == "人才>") {
        var path = "/talent";
        this.$router.push(path); //路由跳转
      } else if (this.last_last_location == "协会>") {
        var path = "/enterprise";
        this.$router.push(path); //路由跳转
      } else if (this.last_last_location == "服务平台 >") {
        var path = "/";
        this.$router.push(path); //路由跳转
      } else if (this.last_last_location == "资讯>") {
        var path = "/news";
        this.$router.push(path); //路由跳转
      }
    },
    link_1() {
      this.$router.push("/");
    }
  }
};
</script>

<style lang="scss" scoped>
#x_currentlocation div {
  display: inline-block;
  cursor: pointer;
  
}
#x_currentlocation{
  background:transpaent;
  
}
.now_location {
  // color:red;
  cursor: pointer;
}
</style>


