<template>
  <div id="exhibition">
    <keep-alive>
      <router-view @select="routerLink"></router-view>
    </keep-alive>
  </div>
</template>

<script>
export default {
  name: "exhibition",
  data() {
    return {};
  },
  methods: {
    routerLink(path, item) {
      this.$router.push({ path: path, query: { item } }); //路由跳转
    }
  },
  components: {
  }
};
</script>

<style lang="scss" scoped>
</style>




