<template>
  <div id="talent-list">
    <div class="left_block">
      <!-- 位置路径 -->
      <div class="box1">
        <div class="curr_position">
          当前位置：
          <a @click="selectItem('/home')">服务平台</a>
          <span>></span>
          <a @click="selectItem(path[0])">{{init_data.belong}}</a>
          <span>></span>
          <a @click="selectItem(path[1])">{{init_data.name}}</a>
        </div>
      </div>
      <!-- 目录 -->
      <div class="box3">
        <div class="content_title">{{this.init_data.catalog}}</div>
        <ul class="content_list">
          <li v-for="(item,index) in items" @click="routerLink(index)">
            {{item.memberName}}
          </li>
        </ul>
      </div>
    </div>
    <div class="right_block">
      <!-- 广告招收 -->
      <div class="box4">
        <ul class="title">
          <li class="list_title">
            <a style="margin-left:15px;" class="active">广告招收栏目</a>
          </li>
        </ul>
        <div style="height:270px;width:100%;">
          <keep-alive>
            <!-- <advertise-list></advertise-list> -->
          </keep-alive>
        </div>
      </div>
      <!-- 用户 -->
      <div class="box4">
        <ul class="title" style="margin-left:15px;">
          <li class="list_title" v-for="(tab,index) in tabs" v-if="tab.belong === '用户'" :key="tab.name" @mouseover="tabLink(index,tab.tocpn)">
            <a :class="{active:tab.isActive}">{{tab.name}}</a>
          </li>
        </ul>
        <div style="height:270px;width:100%;">
          <keep-alive>
            <pb-list :is="currentTab[6]"></pb-list>
          </keep-alive>
        </div>
      </div>
      <!-- 活动 -->
      <div class="box4">
        <ul class="title" style="margin-left:15px;">
          <li class="list_title" v-for="(tab,index) in tabs" v-if="tab.belong === '活动'" :key="tab.name" @mouseover="tabLink(index,tab.tocpn)">
            <a :class="{active:tab.isActive}">{{tab.name}}</a>
          </li>
        </ul>
        <div style="height:270px;width:100%;">
          <keep-alive>
            <pb-list :is="currentTab[7]"></pb-list>
          </keep-alive>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import NormalList from "@/components/common/normal-list";

export default {
  name: "talent-list",
  data() {
    return {
      currentTab: [
        "pb-list",
        "pb-list",
        "pb-list2",
        "pb-list",
        "pb-list3",
        "pen-list",
        "",
        ""
      ], //各小标题块内容
      tabs: [
        {
          name: "用户中心",
          belong: "用户", //归属的小标题
          path: null, //点击跳转
          tocpn: "", //子组件名
          content: null, //组件内容，对象数组
          isActive: true
        },
        {
          name: "订阅资讯",
          belong: "用户", //归属的小标题
          path: null, //点击跳转
          tocpn: "normal-list", //子组件名
          content: null, //组件内容，对象数组
          isActive: false
        },
        {
          name: "我的交易",
          belong: "用户", //归属的小标题
          path: null, //点击跳转
          tocpn: "normal-list", //子组件名
          content: null, //组件内容，对象数组
          isActive: false
        },
        {
          name: "近期活动",
          belong: "活动", //归属的小标题
          path: null, //点击跳转
          tocpn: "", //子组件名
          content: null, //组件内容，对象数组
          isActive: true
        },
        {
          name: "协会活动",
          belong: "活动", //归属的小标题
          path: null, //点击跳转
          tocpn: "", //子组件名
          content: null, //组件内容，对象数组
          isActive: false
        },
        {
          name: "其他",
          belong: "活动", //归属的小标题
          path: null, //点击跳转
          tocpn: "", //子组件名
          content: null, //组件内容，对象数组
          isActive: false
        }
      ],
      init_data: {
        name: "",
        belong: "",
        catalog: ""
      },
      items: [
        {
          memberName: "伍邵宁",
          id: "1",
          positionId: 1,
          joinTime: "2017-10-30",
          introduction: null,
          faceImg: null
        },
        {
          memberName: "伍邵宁",
          id: "1",
          positionId: 1,
          joinTime: "2017-10-30",
          introduction: null,
          faceImg: null
        },
        {
          memberName: "萧俊威",
          id: "1",
          positionId: 1,
          joinTime: "2017-10-30",
          introduction: null,
          faceImg: null
        },
        {
          memberName: "伍邵宁",
          id: "1",
          positionId: 1,
          joinTime: "2017-10-30",
          introduction: null,
          faceImg: null
        },
        {
          memberName: "萧俊威",
          id: "1",
          positionId: 1,
          joinTime: "2017-10-30",
          introduction: null,
          faceImg: null
        },
        {
          memberName: "伍邵宁",
          id: "1",
          positionId: 1,
          joinTime: "2017-10-30",
          introduction: null,
          faceImg: null
        },
        {
          memberName: "萧俊威",
          id: "1",
          positionId: 1,
          joinTime: "2017-10-30",
          introduction: null,
          faceImg: null
        },
        {
          memberName: "伍邵宁",
          id: "1",
          positionId: 1,
          joinTime: "2017-10-30",
          introduction: null,
          faceImg: null
        },
        {
          memberName: "萧俊威",
          id: "1",
          positionId: 1,
          joinTime: "2017-10-30",
          introduction: null,
          faceImg: null
        },
        {
          memberName: "伍邵宁",
          id: "1",
          positionId: 1,
          joinTime: "2017-10-30",
          introduction: null,
          faceImg: null
        },
        {
          memberName: "萧俊威",
          id: "1",
          positionId: 1,
          joinTime: "2017-10-30",
          introduction: null,
          faceImg: null
        },
        {
          memberName: "伍邵宁",
          id: "1",
          positionId: 1,
          joinTime: "2017-10-30",
          introduction: null,
          faceImg: null
        },
        {
          memberName: "萧俊威",
          id: "1",
          positionId: 1,
          joinTime: "2017-10-30",
          introduction: null,
          faceImg: null
        },
        {
          memberName: "伍邵宁",
          id: "1",
          positionId: 1,
          joinTime: "2017-10-30",
          introduction: null,
          faceImg: null
        },
        {
          memberName: "萧俊威",
          id: "1",
          positionId: 1,
          joinTime: "2017-10-30",
          introduction: null,
          faceImg: null
        },
        {
          memberName: "伍邵宁",
          id: "1",
          positionId: 1,
          joinTime: "2017-10-30",
          introduction: null,
          faceImg: null
        },
        {
          memberName: "萧俊威",
          id: "1",
          positionId: 1,
          joinTime: "2017-10-30",
          introduction: null,
          faceImg: null
        },
        {
          memberName: "伍邵宁",
          id: "1",
          positionId: 1,
          joinTime: "2017-10-30",
          introduction: null,
          faceImg: null
        },
        {
          memberName: "萧俊威",
          id: "1",
          positionId: 1,
          joinTime: "2017-10-30",
          introduction: null,
          faceImg: null
        },
        {
          memberName: "伍邵宁",
          id: "1",
          positionId: 1,
          joinTime: "2017-10-30",
          introduction: null,
          faceImg: null
        },
        {
          memberName: "萧俊威",
          id: "1",
          positionId: 1,
          joinTime: "2017-10-30",
          introduction: null,
          faceImg: null
        }
      ],
      path: ["", ""]
    };
  },

  methods: {
    init() {
      var data = JSON.parse(this.$route.query.item); //接收跳转数据
      this.init_data.name = data.name;
      this.init_data.belong = data.belong;
      var url;
      if (this.init_data.name == "协会会员") {
        this.items = this.items.slice(0, 1);
        this.path[1] = "/association/vip-list";
        this.path[0] = "/association";
        this.init_data.catalog = "会员目录";
        let requestPath = data.requestPath;
        url = this.HOST + requestPath;
        this.axios({
          method: "post",
          url: url,
          data: {
            pageNum: 0
          }
        })
          .then(response => {
            this.items = response.data.message.list;
          })
          .catch(response => {
            console.log(response);
          });
      }
      if (this.init_data.name == "产品库") {
        this.items = [];
        this.path[1] = "/product/product-list";
        this.path[0] = "/product";
        this.init_data.catalog = "产品目录";
        let requestPath = "/product/getProductList";
        url = this.HOST + requestPath;
        this.axios({
          method: "post",
          url: url,
          data: {
            pageNum: 0
          }
        })
          .then(response => {
            for (let i in response.data.message) {
              let obj = new Object();
              this.items.push(obj);
              this.items[i].memberName = response.data.message[i].name;
              this.items[i].id = response.data.message[i].id;
              this.items[i].introduction =
                response.data.message[i].introduction;
            }
          })
          .catch(response => {
            console.log(response);
          });
      }
      if (this.init_data.name == "专家库") {
        this.items = [];
        this.path[1] = "/talent/talent-list";
        this.path[0] = "/talent";
        this.init_data.catalog = "专家目录";
        let requestPath = "/expert/findExpertByPage";
        url = this.HOST + requestPath;
        this.axios
          .get(url, {
            params: {
              pageNum: 0
            }
          })
          .then(response => {
            for (let i in response.data.message) {
              let obj = new Object();
              this.items.push(obj);
              this.items[i].prototype.memberName = response.data.message[i].name;
              this.items[i].prototype.id = response.data.message[i].id;
              this.items[i].prototype.introduction =
                response.data.message[i].introduction;
            }
          })
          .catch(response => {
            console.log(response);
          });
      }
      if (this.init_data.name == "高校") {
        this.items = [];
        this.path[1] = "/talent/talent-list";
        this.path[0] = "/talent";
        this.init_data.catalog = "高校目录";
        let requestPath = "/expert/findUniversityByPage";
        url = this.HOST + requestPath;
        this.axios
          .get(url, {
            params: {
              pageNum: 0
            }
          })
          .then(response => {
                            console.log(this.items)
            for (let i in response.data.message) {
              let obj = new Object();
              this.items.push(obj);
              this.items[i].prototype.memberName = response.data.message[i].name;
              this.items[i].prototype.id = response.data.message[i].id;
              this.items[i].prototype.introduction =
                response.data.message[i].introduction;
            }
                            console.log(this.items)

          })
          .catch(response => {
            console.log(response);
          });
      }
    },
    selectItem(item) {
      this.$emit("select", item);
    }, //将item通过select传给父组件
    // routerLink(path){
    //   this.$router.push(path);//路由跳转
    // },
    routerLink(index) {
      let id = this.items[index].id;
      let data = this.init_data;
      let address;
      if (this.init_data.name == "协会会员")
        address = "/association/vip-detail";
      if (this.init_data.name == "高校" || this.init_data.name == "专家库")
        address = "/talent/talent-detail";
      this.$router.push({ path: address, query: { id, data } });
    },
    tabLink(index, tocpn) {
      //更改高亮标签
      this.$set(
        this.tabs,
        this.tabs[index],
        (this.tabs[index].isActive = true)
      );
      for (var i = 0; i < this.tabs.length; i++) {
        if (this.tabs[i].belong == this.tabs[index].belong && i != index) {
          this.$set(this.tabs, this.tabs[i], (this.tabs[i].isActive = false)); //解决methods中无法更改data数据的问题
        }
      }
      if (tocpn != null) {
        var t = 6;
        if (this.tabs[index].belong == "用户") t = 6;
        else if (this.tabs[index].belong == "活动") t = 7;

        this.currentTab[t] = this.tabs[index].tocpn;
        //更改小标题块内容
      }
    }
  },
  components: {
    NormalList
  },
  activated() {
    this.init();
  } //组件被激活时调用
};
</script>

<style lang="scss" scoped>
@mixin left {
  margin: 5px auto;
  width: 795px;
  float: left;
  box-sizing: border-box;
  overflow: hidden;
}
@mixin right {
  margin: 5px auto;
  width: 390px;
  float: right;
  box-sizing: border-box;
  overflow: hidden;
}
@mixin i-box {
  -webkit-box-shadow: 0 0 1px #bebebe;
  box-shadow: 0 0 1px #bebebe;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border: 1px solid rgba(204, 204, 204, 0.377);
  height: 100%;
  overflow: hidden;
  box-sizing: border-box;
  background: rgba(255, 255, 255, 0.95);
}
.active {
  color: #127de7 !important;
  border-bottom: 2px solid #127de7;
  padding-bottom: 4px;
}
.active2 {
  & {
    box-sizing: border-box;
    color: #535353;
    font-weight: 600;
  }
  &::after {
    content: "";
    position: absolute;
    bottom: -3px;
    left: 0;
    width: 100%;
    height: 4px;
    border-bottom: #127de7 3px solid;
    z-index: 99;
  }
}
#talent-list {
  margin: 5px auto;
  width: 1200px;
  overflow: hidden;
}
.left_block {
  @include left;
}
.right_block {
  @include right;
}
.box1,
.box2,
.box3 {
  @include left;
  @include i-box;
}
.box1 {
  & {
    color: #404040;
    font-weight: 500;
    padding: 0 15px;
    overflow: hidden;
  }
  .curr_position {
    & {
      margin-top: 14px;
      margin-bottom: 15px;
    }
    a {
      text-decoration: none;
      cursor: pointer;
    }
  }
}
.box3 {
  color: #404040;
  padding: 20px 45px;
}
.content_title {
  color: #272222;
  font-size: 25px;
  font-weight: 500;
}
.content_list {
  color: #616161;
  margin: 10px 0;
  padding: 5px 20px;
}
.content_list li {
  list-style: disc;
  margin: 5px 0;
  cursor: pointer;
}
.content_list li:hover {
  color: #232424;
  font-size: 18px;
}
.box4 {
  @include right;
  @include i-box;
}
.title {
  margin-bottom: 12px;
  margin-top: 2px;
  width: 100%;
  height: 28px;
  line-height: 28px;
}
.title li {
  float: left;
}
.list_title a {
  color: #7b7b7b;
  cursor: default;
  font-size: 1em;
  font-weight: 500;
  line-height: 25px;
  vertical-align: middle;
  margin-right: 10px;
}
.list_title a:hover {
  color: #127de7;
  border-bottom: 2px solid #127de7;
  cursor: default;
  padding-bottom: 4px;
}
</style>


