<template>
  <div id="questionnare">
    <div class="box">
      <!-- 路径 -->
      <div class="box1">
        <div class="curr_position">
          当前位置：
          <a @click="selectItem('/home')">服务平台</a>
          <span>></span>
          <a>问卷调查</a>
        </div>
      </div>
      <!-- 该企业发起的问卷 -->
      <div class="box2">
        <!-- 左侧导航 -->
        <div class="l-slide"></div>
        
      </div>
      <!--  -->
      <div class="box3"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "questionnare",
  data() {
    return {};
  },
  methods: {
    selectItem(path, data) {
      let item = JSON.stringify(data);
      this.$emit("select", path, item);
    } //将item通过select传给父组件
  }
};
</script>


<style lang="scss" scoped>
#questionnare {
  margin: 5px auto;
  width: 1200px;
  overflow: hidden;
}
@mixin i-box {
  -webkit-box-shadow: 0 0 1px #bebebe;
  box-shadow: 0 0 1px #bebebe;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border: 1px solid rgba(204, 204, 204, 0.377);
  height: 100%;
  overflow: hidden;
  box-sizing: border-box;
}
// .box,
.box1 {
  @include i-box;
}
.box1 {
  & {
    color: #404040;
    font-weight: 500;
    padding: 0 15px;
    overflow: hidden;
    margin: 5px auto;
    width: 795px;
    float: left;
    box-sizing: border-box;
    background: rgba(255, 255, 255, 0.95);
  }
  .curr_position {
    & {
      margin-top: 14px;
      margin-bottom: 15px;
    }
    a {
      text-decoration: none;
      cursor: pointer;
    }
  }
}
</style>



