<template>
  <div id="x_policy">
    <div class="out_line">
      <div class="hot_search">
        <x_hotsearch></x_hotsearch>
      </div>
      <!--左边长列表-->
      <div class="box1-left">
        <div class="box2-left">
           <x_currentlocation v-bind:current_where="current_place1" v-bind:last_location="last_location" v-bind:last_last_location="last_last_location"></x_currentlocation>
        </div>
        <div class="box3-left" >
          <x_cahrtlist_single :morelist="morelist" :url_last="url_last"></x_cahrtlist_single>
        </div>
        <!--下面分页插件-->
        <div class="box-paging">
         <paging :pagenumber="pagenumber" :url_last="url_last"  v-on:backnumber="backnumber_1"></paging>
        </div>
      </div>
      <!--右边长列表-->
      <div class="box4-right">
        <div class="box5-right"><x_industry></x_industry></div>
        <div class="box6-right"><x_tab_switch v-bind:tabt1="tabthree1" v-bind:signal="signal[0].name"></x_tab_switch></div>
        <div class="box7-right"><x_tab_switch v-bind:tabt1="tabthree1" v-bind:signal="signal[1].name"></x_tab_switch></div>
        <div class="box8-right"><x_singlesidebar v-bind:tab_three1="tabthree1[11]" :isRequested='isRequested'></x_singlesidebar></div>
      </div>
    </div>
  </div>
</template>
<script>
import MHeader from "@/components/common/m-header";
import x_currentlocation from "../news/currentlocation.vue";
import x_singlesidebar from "../news/single_sidebar.vue";
import x_tab_switch from "../news/x_tab_switch.vue";
import x_hotsearch from "@/components/common/hotsearch.vue";
import Title from "@/components/news/Title.vue";
import x_Text from "@/components/news/Text.vue";
import x_related_reading from "@/components/news/related_reading.vue";
import x_statement from "@/components/news/x_statement.vue";
import x_comment from "@/components/news/comment.vue";
import x_chartlist from "@/components/news/chartlist.vue";
import x_cahrtlist_single from "@/components/news/charlist_single.vue";
import x_industry from "@/components/news/x_industry.vue";
import paging from "@/components/common/paging.vue";
import Qs from 'qs'

export default {
  name: "policy_text",
  data() {
    return {
      last_location: "政策",
      last_last_location: "",
      current_place1: "",
      tabthree1: [
        {
          name: "用户中心",
          belong: "用户",//属于哪个组件
          path: null, //点击跳转
          tocpn: "first", //子组件名
          message: null, //组件内容，对象数组
          isActive: true
        },
        {
          name: "订阅资讯",
          belong: "用户",//属于哪个组件
          path: null, //点击跳转
          tocpn: "second", //子组件名
          message: null, //组件内容，对象数组
          isActive: false
        },
        {
          name: "我的交易",
          belong: "用户",//属于哪个组件
          path: null, //点击跳转
          tocpn: "third", //子组件名
          message: null, //组件内容，对象数组
          isActive: false
        },
        {
          name: "近期活动",
          belong: "活动",//属于哪个组件
          path: null, //点击跳转
          tocpn: "first", //子组件名
          message: null, //组件内容，对象数组
          isActive: true
        },
        {
          name: "协会活动",
          belong: "活动",//属于哪个组件
          path: null, //点击跳转
          tocpn: "second", //子组件名
          message: null, //组件内容，对象数组
          isActive: false
        },
        {
          name: "其他",
          belong: "活动",//属于哪个组件
          path: null, //点击跳转
          tocpn: "third", //子组件名
          message: null, //组件内容，对象数组
          isActive: false
        },
        {
          name: "行业动态",
          belong: "资讯",//属于哪个组件
          path: null, //点击跳转
          tocpn: "", //子组件名
          message: null, //组件内容，对象数组
          isActive: false
        },
        {
          name: "资讯热点",
          belong: "资讯",//属于哪个组件
          path: null, //点击跳转
          tocpn: "", //子组件名
          message: null, //组件内容，对象数组
          isActive: false
        },
        {
          name: "政策",
          belong: "资讯",//属于哪个组件
          path: null, //点击跳转
          tocpn: "", //子组件名
          message: null, //组件内容，对象数组
          isActive: false
        },
        {
          name: "行业报告",
          belong: "资讯",//属于哪个组件
          path: null, //点击跳转
          tocpn: "", //子组件名
          message: null, //组件内容，对象数组
          isActive: false
        },
        
        {
          name: "相关热点",
          belong: "相关热点",//属于哪个组件
          path: null, //点击跳转
          tocpn: "", //子组件名
          message: null, //组件内容，对象数组
          isActive: false
        },
        {
          name: "广告招商",
          belong: "广告招商",//属于哪个组件
          path: null, //点击跳转
          tocpn: "", //子组件名
          message: null, //组件内容，对象数组
          isActive: false
        }
        
      ],
      tabthree2: [
        {
          message: "近期活动"
        },
        {
          message: "协会活动"
        },
        {
          message: "其他"
        }
      ],
      morelist:{},
      pagenumber:{},
      url_last:"/information/findPolicyList",
      signal:[
        {
          name:"用户"
        },
        {
          name:"活动"
        }
      ],
      isRequested:false
    };
  },
  components: {
    MHeader,
    x_industry,
    x_currentlocation,
    x_singlesidebar,
    x_tab_switch,
    x_hotsearch,
    Title,
    x_Text,
    x_related_reading,
    x_statement,
    x_comment,
    x_chartlist,
    paging,
    x_cahrtlist_single
  },
  methods:{
      backnumber_1(val){
      this.morelist=val;
      this.pagenumber=val;
      console.log(val);
    },
    init() {
      var data_1 =Qs.stringify({
          pageNum:1 //页数，默认为1
        });
      this.axios.get(this.HOST+"/information/findPolicyList", {
          params: {
            pageNum:1
          }
        })
        .then(response => {
          console.log(response.data);
          this.morelist=response.data.message;
          this.pagenumber=response.data.message;
          console.log(this.pagenumber);
        })
          .then(response => {
            console.log(response.data);
          })
          .catch(function(response) {
            console.log(response.data);
          });
      }
  },
  activated(){
    this.init();
  }
};
</script>

<style lang="scss" scoped>
@mixin adv {
  margin: 5px auto;
  width: 1200px;
  height: 125px;
  //overflow: hidden;
}
@mixin w-box {
  margin: 5px auto;
  width: 1200px;
  height: 280px;
  overflow: hidden;
  margin-bottom: 18px;
}
@mixin i-box {
  box-shadow: 0 0 1px #bebebe;
  height: 100%;
  overflow: hidden;
}
/*右边小框重复样式*/
@mixin litter_one_tab_box {
  @include i-box;
  width: 390px;
  height: 350px;
  float: right;
  margin: 10px 0;
}
.hot_search {
  @include adv;
  margin:0;
  height:100%;
  width:auto;
  padding: 13px;
  //border: 1px solid rgba(204, 204, 204, 0.377);
  //margin-left:15px;
  background: rgba(255, 255, 255, 0.95);
}
.box1-left {
  width: 795px;
  float: left;
  //@include i-box;
  //border: 1px solid rgba(204, 204, 204, 0.377);
  //margin-left:15px;
}
.box2-left {
  //@include i-box;
  background: rgba(255, 255, 255, 0.95);
  margin: 10px 0;
  padding: 13px;
}
.out_line {
  @include adv;
  height: 100%;
}
/*左边文章样式*/
.box3-left {
  //@include i-box;
  margin: 10px 0;
  background: rgba(255, 255, 255, 0.95);
}
.box4-right {
  width: 390px;
  float: right;
  div{
    background: rgba(255, 255, 255, 0.95);
  }
}
.box5-right,
.box6-right,
.box7-right ,
.box8-right{
  @include litter_one_tab_box;
  box-shadow: 0 0 1px #bebebe;
  margin-right: 0.5px;
}
.box-paging {
  //@include i-box;
  height: 50px;
  margin: 10px 0;
}
</style>


