<template>
  <div id="app">
    <div class="bg-img">
      <div class="bg-content">
        <m-header ref="c1" @controlSign="controlSign1"></m-header>
        <keep-alive>
          <router-view></router-view>>
        </keep-alive>
        <sign id="sign" ref="sign" v-show="showSign" @controlSign="controlSign1"></sign>
        <div v-if="showSign" class="background-wall"></div>
        <m-footer></m-footer>
        <!-- <canvas id='mycanvas'></canvas> -->
      </div>
    </div>
  </div>
</template>

<script>
import MHeader from "./components/common/m-header";
import MFooter from "./components/common/m-footer";
import Sign from "@/components/sign/sign";

// var WIDTH = document.documentElement.clientWidth, HEIGHT = document.documentElement.clientHeight, POINT = 400;

export default {
  name: "hello",
  components: {
    MHeader,
    MFooter,
    Sign
  },
  //监听路由变化获取当前路由
  watch: {
    $route() {
      // 获取当前路径
      let path = this.$route.path;
      // 检索当前路径
      this.$refs.c1.checkRouterLocal(path);
    }
  },
  methods: {
    controlSign1(result, type) {
      let that = this;
      //判断跳出页面
      if (type) {
        this.$refs.sign.selectPage(type);
        setTimeout(function() {
          //显示跳出页面
          that.showSign = result;
        }, 200);
      } else {
        this.showSign = result;
      }
    }
  },
  mounted: function() {
    // 获取当前路径
    let path = this.$route.path;
    // console.log(path);
    // 检索当前路径
    this.$refs.c1.checkRouterLocal(path);
  }, //确保刷新后页面初始化不出错
  data() {
    return {
      path: "",
      showSign: false
    };
  }
};
</script>

<style lang="scss">
body {
  margin: 0;
  font-size: 16px;
  font-family: "微软雅黑", "Microsoft Yahei", Verdana, sans-serif;
  min-width: 1002px;
  // background: url(/static/PICS/ui-background.jpg) center top repeat rgb(255, 255, 255);
  // background: url(http://image.woshipm.com/wp-files/2014/09/72f9850c5a1e72f4ea873584fbb3959c.jpg) center top repeat rgb(255, 255, 255);
}
.bg-img {
  position: relative;
  // width: 100%;
  min-width: 1200px;
  height: 100%;
  background: url(http://image.woshipm.com/wp-files/2014/09/72f9850c5a1e72f4ea873584fbb3959c.jpg)
    center top repeat;
}
.bg-img::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-image: linear-gradient(to bottom right, #26486e, #cc998f);
  opacity: 0.3;
  z-index: 1;
}
.bg-content {
  z-index: 2;
  position: relative;
}
ul,
li {
  padding: 0;
  margin: 0;
  list-style: none;
}
input:focus {
  outline: none;
}
a {
  text-decoration: none;
  color: inherit;
}
.background-wall {
  background-color: #000000;
  width: 100%;
  height: 100%;
  top: 0;
  position: fixed;
  clear: both;
  z-index: 5;
  opacity: 0.5;
}
</style>


