<template>
  <div id="about">
    <form class="nav-bar-search">
        <div class="nav-left">
            <div class="nav-search-scope nav-sprite" @mouseover="function(){istrue=false;}" @mouseout="function(){istrue =true;}" >
                <div class="nav-search-facade" >
                    <span class="nav-search-label">{{navpartchoose}}</span>
                    <i class="nav-icon" ></i>
                    <div class="nav-list" :class="{isit:istrue}" @mouseover="function(){istrue=false;}" >
                        <li v-for="(li,key) in nav_list" :key="key" class="navlist"  @click="function(){navpartchoose =li.name;}">{{li.name}}</li>
                    </div>
                </div>
            </div>
        </div>
        <div class="nav-right">
            <div class="nav-search-submit">
                <span class="nav-search-text">搜索</span>
                <input class="nav-input"></input>
            </div>

        </div>
        <div class="nav-fill">
            <div class="nav-search-field">
                <input class="tabsearchbox"></input>
            </div>
        </div>
    </form>  
  </div>
</template>

<script>


export default {
  name: "hot-search2",
  data(){
    return {
        navpartchoose:"全部",
        nav_list:[{
            name:"产品推荐",
            isActive:false
        },
        {
            name:"行业报告",
            isActive:false
        },
        {
            name:"政策",
            isActive:false
        },
        {
            name:"技术标准",
            isActive:false
        },],
        istrue:true,
    }
  },
  components: {
    
  },
}
</script>

<style lang="scss" scoped>
.isit{
    display:none!important;
}
.nav-list{
    opacity:1;
    position:absolute;
    top:40px;
    right:0px;
    background: white;
    z-index: 888;
}
.navlist{
    padding:5px;
    text-align: center;
    width:75px;
}
.navlist:hover{
    background-color:#127de7;
    color:white;
    cursor:pointer;
}
.nav-bar-search{
    display: block;
    position: relative;
}
.nav-left{
    position: relative;
    height: 38px;
}
.nav-search-scope{
    width:80px;
    position: relative;
    float: left;
    height: 36px;
    margin: 0;
    padding-left: 5px;
    //overflow: hidden;
    border: 1px solid #cdcdcd;
    border-top-color: #bdbdbd;
    -webkit-border-radius: 4px 0 0 4px;
    -moz-border-radius: 4px 0 0 4px;
    border-radius: 4px 0 0 4px;
    border-radius: 0\9;
    *border-radius: 0;
    background-position: 0 -532px;
}
.nav-search-facade{
    position: relative;
    //float: left;
    cursor: default;
    //overflow: hidden;
    font-size: 13px;
    line-height: 19px;
    color: #111;
    font-family: Arial,sans-serif;
}
nav-search-label{
    display: block;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    color: #666;
    font-size: 12px;
    line-height: 33px;
    margin-right: 21px;
    margin-left: 5px;
}
.nav-icon{
    
    position: absolute;
    top: 14px;
    right: 8px;
    border-style: solid;
    _border-style: dashed;
    border-width: 4px;
    border-color: transparent;
    border-top: 4px solid #666;
    border-bottom-width: 0;
    width: 0;
    height: 0;
    font-size: 0;
    line-height: 0;
    background-image: url(../../assets/hotsearch2.png);
    background-position: 0 1000px;
    background-repeat: repeat-x;
}
.nav-right{
    position: relative;
    height: 36px;
    left:300px;
    width:36px;
    background: #5f5f6f;
    border: 1px solid;
    border-color: #485059 #2c3137 #363c43 #485059;
    border-radius: 0 4px 4px 0;
    border-radius: 0\9;
}
.nav-search-submit{
    background-image: url(../../assets/hotsearch2.png);
    background-size: 350px;
    //position: relative;
    vertical-align: middle;
    margin: 0 auto;
    height: 35px;
    width: 42px;
    overflow: hidden;
    cursor: pointer;
    //border: 1px solid;
    //border-color: #485059 #2c3137 #363c43 #485059;
    background-color: #5b626a;
    -webkit-border-radius: 0 4px 4px 0;
    -moz-border-radius: 0 4px 4px 0;
    border-radius: 0 4px 4px 0;
    border-radius: 0\9;
    *border-radius: 0;
    background-position: 0 -381px;
}
.nav-search-submit-text,.nav-search-submit{
    background-position: -40px -290px;
    width: 21px;
    height: 21px;
    text-indent: -500px;
    //top: 7px;
    position: absolute;
    left: 7px;
    top: 6px;
    font-weight: 700;
    color: #fff;
    font-size: 13px;
    line-height: 13px;
    font-family: arial,sans-serif;
    vertical-align: middle;
    text-align: center;
    text-shadow: 0 -1px 0 rgba(0,0,0,.25);
}
.nav-search-submit-text{

    position: absolute;
    left: 11px;
    top: 10px;
    font-weight: 700;
    color: #fff;
    font-size: 13px;
    line-height: 13px;
    font-family: arial,sans-serif;
    vertical-align: middle;
    text-align: center;
    text-shadow: 0 -1px 0 rgba(0,0,0,.25);
    left: 12px;
    color: #111;
    top: 11px;
    text-shadow: none;
}
.nav-input{
    position: relative;
    display: block;
    height: 100%;
    width: 100%;
    font-size: 14px;
    padding: 0;
    margin: 0;
    cursor: pointer;
    outline: 0;
    color: #fff;
    background: 0 0;
    border: none;
    text-indent: -1000px;
    line-height: 1px; 
}
.nav-search-field{

    
}
.nav-search-label{
    position: relative;
    display: inline-block;
    *display: inline;
    *zoom: 1;
    float: left;
    //line-height: 35px;
    color: #333;
    font-weight: 700;
    padding: 0 10px 0 0;
    margin-top: 10px;
    font-family: arial,sans-serif;
}
.tabsearchbox{
    width:300px;
    left: 87px;
    position: absolute;
    top: 0;
    display: block;
    //width: 100%;
    //line-height: 15px;
    //padding: 6px 10px 6px 0;
    margin: 0;
    border: 0;
    height: 35px;
    font-family: arial,sans-serif;
    font-size: 15px;
    background: 0 0;
    color: #111;
    outline: 0;
    -webkit-appearance: none;
    box-shadow: none;
    text-indent: 8px;
    background: #fff;
    border-top: 1px solid #bdbdbd;
    border-bottom: 1px solid #cdcdcd;
}
.nav-right,.nav-left,.nav-search-field{
    display:inline-block;
}
.nav-fill{

    height:0px;
}


</style>



