<template>
  <div id="x_tab_switch">

    <nav>
      <ul class="x_tab_switch_ul">
        <li class="x_tab_switch_li" v-for="(tab,index) in tabt1" :key="tab.name" v-if="tab.belong == signal"><a href="javascript:void(0)" @mouseover="switchTab(index,tabt1)" v-bind:class="{active:tab.isActive}" >{{tab.name}}</a></li>
      </ul>
      <transition mode="out-in">
        <first :is="currentView" keep-alive></first>
      </transition>
    </nav>

  </div>
</template>

<script>
import first from "./first.vue";
import second from "./second.vue";
import third from "./third.vue";
export default {
  data() {
    return {
      currentView: "first",
      isHide: false,
      current: 0,
      tabnow:[
        {
          name: "用户",
          list: "",
          child: "first"
        },
        {
          name: "活动",
          list: "",
          child: "first"
        }
      ],
    };
  },
  props:{
    tabt1:{
      type:Array,
      required:true
    },
    signal:{
      type:String,
      required:true
    }
  },

  components: {
    first,
    second,
    third
  },

  methods: {
    init(){
      console.log(this.signal);
      switch(this.signal){
        case "用户":this.currentView = this.tabnow[0].child;break;
        case "活动":this.currentView = this.tabnow[1].child;break;
      }
    },
    switchTab(index,tabt1){
      for(var i= 0 ;i<tabt1.length;i++){  //不是i<tabt1.length-1
        if(tabt1[i].belong == tabt1[index].belong){
          tabt1[i].isActive = false;
          console.log(1);
        }
      }
      tabt1[index].isActive = true;
      switch(tabt1[index].belong){
        case "用户":this.tabnow[0].child = tabt1[index].tocpn;this.currentView = this.tabnow[0].child;break;
        case "活动":this.tabnow[1].child = tabt1[index].tocpn;this.currentView = this.tabnow[1].child;break;  //这里用mounted也可以
      }
    }
  },
  activated(){
    this.init();
  }
};
</script>

<style lang="scss" scoped>
#x_tab_switch {
  //margin-left: 15px;
  overflow: hidden;
  text-align: left;
  min-height: 277px;
      height: inherit;
}
nav {
  width: 600px;
  //padding: 0 10px;
  &：a {
    text-decoration: none;
    color: #000;
    display: inline-block;
    width: 150px;
    text-align: center;
    background: #aaaaaa;
    padding: 10px;
  }
}
.v-enter,
.v-leave-to {
  opacity: 0;
  transform: translateX(200px);
}
.v-enter-active,
.v-leave-active {
  transition: all 1s ease;
}
ul {
  display: block;
  height: 30px;
  padding: 5px;
  background:white;
}
.x_tab_switch_li {
  margin: 0 8px;
  font-size: 14px;
  list-style: none;
  padding: 0;
  float: left;
  font-family: "微软雅黑", "Microsoft Yahei" !important;
}
li a {
  color: #7b7b7b;
  cursor: default;
  font-size: 14px;
  list-style-type: none;
  text-align: left;
  line-height: 28px;
}
a {
  margin: 10px 0;
}
li {
  margin: 10px 0;
}
.active {
  color: #127de7;
  border-bottom: 2px solid #127de7;
  cursor: default;
  padding-bottom: 4px;
  transition: 0.5s;
}
</style>


