<template>
  <div id="association-introduce">
    <div class="img">
      <img src="../../../static/PICS/association.png" alt="">
    </div>
    <div class="content">
      <div class="name"><span>【名称】</span>广东省机器人协会</div>
      <div class="time"><span>【成立时间】</span>2015年5月16日</div>
      <div class="introduction"><span>【简介】</span><br>广东省机器人协会是由广东省内从事机器人相关设备设计、制造、应用的大专院校、科研机构和生产、销售的企事业单位以及从事相同性质经济活动的经济组织等自愿发起组建的全省性、专业性、非营利性并具有独立法人资格的社会团体。<span class="more">【详细信息】</span></div>
      <div class="boss">
        <span>【轮值会长】</span>杨煜俊
        <span>【会长】</span>孙志强
        <span>【执行会长】</span>任玉桐
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "association-introduce",
  data() {
    return {};
  }
};
</script>

<style lang="scss" scoped>
@mixin max {
  width: 100%;
  height: 100%;
}
#association-introduce {
  @include max;
  // border: 1px solid rgba(204, 204, 204, 0.575);
}
.img {
  & {
    float: left;
    height: 100%;
    display: inline-block;
    margin: 0 30px;
  }
  img {
    margin-top: 15px;
    width: 165px;
    height: 220px;
  }
}
.content {
  & {
    overflow: hidden;
    color: #3f3f3f;
    height: 100%;
    margin: 0 5px;
    box-sizing: border-box;
    line-height: 2;
    width: 500px;
    display: inline-block;
  }
  div {
    span {
      color: #ed1c22;
    }
  }
}
.introduction {
  & {
  }
  span {
  }
  .more {
    cursor: pointer;
  }
  .more:hover {
    color: #ce2126;
  }
}
</style>



