<template>
  <div id="x_Text">

    <!-- <m-header></m-header> -->

    <div v-html="this.content" v-if="this.content!=null"></div>
    <div  v-if="this.content==null||this.content==''" class="NULL_content">{{Text}}</div>

  </div>
</template>

<script>
export default {
  data() {
    return {
      Text:
         "抱歉，这个文章内容好像没有"
    };
  },
  props:{
    content:{  
      required:true
    }
  }
};
</script>

<style lang="scss" scoped>
#x_text {
  width: 100%;
  text-indent: 2em;
  height: 100%;
}
.NULL_content{
  height:600px;
}
</style>




