          this.tabs[12].message = response.data.message.productRecommend;
          this.tabs[13].message = response.data.message.productNews;
          this.tabs[14].message = response.data.message.productEvaluation;
          this.tabs[15].message = response.data.message.productLibrary;
          this.tabs[16].message = response.data.message.science;
          this.tabs[17].message = response.data.message.basic;
          // this.tabs[18].message = response.data.message.case;案例库
          this.tabs[19].message = response.data.message.experts;
          this.tabs[20].message = response.data.message.expertArts;
          this.tabs[21].message = response.data.message.school;

          let t = 0;
          for (let i in this.tabs) {
            if (
              i == 0 ||
              i == 4 ||
              i == 9 ||
              i == 12 ||
              i == 16 ||
              i == 19 ||
              i == 22 ||
              i == 25
            ) {
              this.currentTab[t].cpn = this.tabs[i].tocpn;
              this.currentTab[t].list = this.tabs[i].name;
              this.currentTab[t].newsUrl = this.tabs[i].newsUrl;
              t++;
            }
          } //currentTab初始化
          this.isRequested = true; //组件初始化监听
        })
        .catch(function(response) {
          console.log(response);
        });
    },
    routerLink(path, data) {
      if (path) {
        let address = path;
        let item = JSON.stringify(data); //传递跳转数据
        this.$router.push({ path: address, query: { item } }); //路由跳转
      }
    },
    tabLink(index, tocpn) {
      //更改高亮标签
      this.$set(
        this.tabs,
        this.tabs[index],
        (this.tabs[index].isActive = true)
      );
      for (var i = 0; i < this.tabs.length; i++) {
        if (this.tabs[i].belong == this.tabs[index].belong && i != index) {
          this.$set(this.tabs, this.tabs[i], (this.tabs[i].isActive = false)); //解决methods中无法更改data数据的问题
        }
      }
      //更改组件内容
      var t = 0;
      if (this.tabs[index].belong == "资讯") t = 0;
      else if (this.tabs[index].belong == "协会") t = 1;
      else if (this.tabs[index].belong == "企业") t = 2;
      else if (this.tabs[index].belong == "产品") t = 3;
      else if (this.tabs[index].belong == "技术") t = 4;
      else if (this.tabs[index].belong == "人才") t = 5;
      else if (this.tabs[index].belong == "用户") t = 6;
      else if (this.tabs[index].belong == "活动") t = 7;

      this.currentTab[t].cpn = this.tabs[index].tocpn;
      this.currentTab[t].list = this.tabs[index].name;
      this.currentTab[t].newsUrl = this.tabs[index].newsUrl;
      this.tabMessage[t] = this.tabs[index].message;
      this.isRequested = this.isRequested == true ? false : true; //同组件不同tab切换监听
      // console.log(this.tabMessage[t]);
      //更改小标题块内容
    }
  },
  // computed: {
  //   filterTabs: function() {
  //     var tabs = this.tabs;
  //     return tabs.filter(function(tab) {
  //       return tab.belong == "资讯";
  //     });
  //   }
  // },
  components: {
    MHeader,
    PbList,
    PbList2,
    UlList,
    PersonList,
    PicList,
    PbList3,
    PenList,
    JobList,
    NormalList,
    PbSearch,
    associationIntroduce,
    Advertise,
    Slide,
    Slide2
  },
  // mounted() {
  //   this.init();
  // },
  activated() {
    this.init();
  } //组件被激活时调用
};
</script>

<style lang="scss" scoped>
@mixin adv {
  margin: 5px auto;
  width: 1200px;
  height: 125px;
  overflow: hidden;
}
@mixin w-box {
  margin: 5px auto;
  width: 1200px;
  height: 312px;
  overflow: hidden;
  margin-bottom: 18px;
}
@mixin i-box {
  box-shadow: 0 0 1px #bebebe;
  height: 100%;
  overflow: hidden;
  box-sizing: border-box;
}
@mixin border {
  -webkit-box-shadow: 0 0 1px #bebebe;
  box-shadow: 0 0 1px #bebebe;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border: 1px solid rgba(204, 204, 204, 0.377);
}
.active {
  color: #127de7 !important;
  border-bottom: 2px solid #127de7;
  padding-bottom: 4px;
}
.advertise {
  @include adv;
  margin-top: 5px;
  margin-bottom: 20px;
}
.box1 {
  @include w-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
.box1-left {
  background: rgba(255, 255, 255, 0.95);
  width: 795px;
  float: left;
  @include i-box;
  @include border;
}
.box1-right {
  background: rgba(255, 255, 255, 0.95);
  @include i-box;
  @include border;
  width: 390px;
  float: right;
}
.box2-left {
  background: rgba(255, 255, 255, 0.95);
  @include i-box;
  @include border;
  width: 406px;
  float: left;
}
.box2-right {
  @include i-box;
  background: rgba(255, 255, 255, 0.95);
  @include border;
  width: 375px;
  float: left;
  margin-left: 15px;
}
.box3-left {
  background: rgba(255, 255, 255, 0.95);
  @include i-box;
  width: 390px;
  float: left;
  @include border;
}
.box3-right {
  background: rgba(255, 255, 255, 0.95);
  width: 795px;
  float: right;
  @include border;
  @include i-box;
}

.fa-title {
  // margin-top: 2px;
  vertical-align: middle;
  font-size: 1.5em;
  color: #333;
}
.title {
  margin-bottom: 10px;
  margin-top: 2px;
  width: 100%;
  background: #fff;
  height: 32px;
  line-height: 28px;
}
.title li {
  float: left;
}
.menu_title {
  float: left;
  display: inline-block;
  padding: 0 10px;
  margin-right: 40px;
  cursor: default;
}
.menu_title span {
  font-size: 1em;
  font-weight: 500;
  color: #404040;
  line-height: 25px;
  vertical-align: middle;
}
.list_title a {
  cursor: pointer;
  color: #7b7b7b;
  font-size: 1em;
  font-weight: 500;
  line-height: 25px;
  vertical-align: middle;
  margin-right: 10px;
}
.list_title a:hover {
  color: #127de7;
  border-bottom: 2px solid #127de7;
  padding-bottom: 4px;
}
</style>




