<template>
  <div id="x_third">
    <li v-for="(li,index) in list " :key="index" class="first_list"><a>{{li}}</a></li>
  </div>
</template>

<script>
export default {
  data() {
    return {list:[
            "这是一条机器人信息",
            "这是一条机器人信息",
            "这是一条机器人信息",
            "这是一条机器人信息",
            "这是一条机器人信息",
            "这是一条机器人信息"

        ]};
  }
};
</script>
  
<style lang="scss" scoped>
#x_third{
  padding-left:20px;
}
.first_list{
    background: none;
    padding: 0;
    margin: 0;
    border-top: none;
    line-height: 1em;
    padding: 8px 0 7px;
    clear: both;
    a{
          color: #3F3F3F;
    display: inline-block;
    max-width: 245px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    line-height: 1em;
    vertical-align: middle;
    }
}
</style>


