<template>
  <div id="productHome">
    <div id="left">
        <div id="location">
            当前位置：<router-link to="/home" class="selectable">服务平台</router-link> > <span class=blue>产品</span>
        </div>
        <div id="productIntroduce">
            <div class="bigTitle">产品推荐</div>
            <ul id="productListHead">
                <li class="productList" v-for="(i,index) in message.productRecommend.slice(listFirst,listFirst + 5)">{{i.title}}</li>
            </ul>
            <button class="pageButton" v-bind:class="{active:index==pageNum}" v-for="(i,index) in Math.ceil(introduceList.length/6)" @click="introducePage(index)">{{index + 1}}</button>
        </div>
        <div id="productStore">
            <div class="bigTitle">产品库</div>
            <div class="flexBox">
                <div class="productFrame" v-for="(i,index) in message.productLibrary">
                    <img class="productPic">
                    <p class="productName">{{i.name}}</p>
                </div>
            </div>
        </div>
    </div>
    <div id="right">
        <div id="productHot">
            <div class="bigTitle">热门产品
            </div>
            <router-link to="/product" v-for="i in productHot" class="smallLink" :key="i">{{i}}<br></router-link>
        </div>
    </div> 
  </div>
</template>

<script>
import MHeader from "@/components/common/m-header";

export default {
  name: "productHome",
  data() {
    return {
        message:undefined,
        params:undefined,
        introduceList:[
            "RV-110E精密减速机1",
            "RV-110E精密减速机2",
            "RV-110E精密减速机3",
            "RV-110E精密减速机4",
            "RV-110E精密减速机5",
            "RV-110E精密减速机6",
            "RV-110E精密减速机7",
            "RV-110E精密减速机8",
            "RV-110E精密减速机9",
        ],
        listFirst:0,
        productHot:["新松机器人科技天团亮相世界机器人大会",
        "2018年上边年广东工业机器人产量占全国1/5",
        "机器视觉公司扩博智能完成1100万美元融资",
        "2018世界机器人大会在北京举办"
        ],
        pageNum:0,
    };
  },
  components: {
    MHeader
  },
  methods:{
      introducePage(num){
          this.listFirst=num*6;
          this.pageNum=num;
      },
      routerLinkName:function(theName,paramValue,paramName){
        this.params=this.params || new Object();
        this.params[paramName]=paramValue;
        console.log("params",this.params)
        this.$router.push({name:theName,params:this.params})
    },
  },
  created:function(){
        this.axios.get(this.HOST + 'common/getIndex')
        .then((res)=>{
            this.message=res.data.message;
        })
  }
};
</script>

<style lang="scss" scoped>
*{
    margin:0;
    padding:0;
}
.flexBox{
    display:flex;
    justify-content:space-around;
    flex-wrap:wrap;
}
.blue{
    color:#127de7;
}
.pageButton{
    margin:0 3px;
    padding:2px 6px;
    border:1px solid black;
    outline:none;
}
.smallLink{
    height:30px;
    line-height:30px;
}
button.active{
    color:white;
    background:black;
}
@mixin border{
    border:2px solid rgba(222,222,222,1);
    margin:10px 0;
    padding:5px;
    max-width:100%;
}
@mixin rightFrame{
}
div#left{
    float:left;
    width:700px;
}
div#right{
    width:480px;
    float:right;
}
div#location{
    @include border;
    width:700px;
      background: rgba(255, 255, 255, 0.95);
}
div#productHome{
    width:1200px;
    margin:0 auto;
}
div#productIntroduce{
    width:700px;
    @include border;
      background: rgba(255, 255, 255, 0.95);
}
div#productStore{
    @include border;
      background: rgba(255, 255, 255, 0.95);
}
div.bigTitle{
    height:22px;
    line-height:22px;
    position:relative;
    margin-bottom:5px;
}
div.bigTitle:after{
    content:"";
    position:absolute;
    bottom:-5px;
    left:0px;
    width:100%;
    border-bottom:2px solid rgb(222,222,222)
}
ul#productListHead{
    display:flex;
    height:200px;
    flex-direction:column;
    justify-content:space-around;
}
li.productList{
    height:25px;
    line-height:25px;
}
div#productStore{
    width:700px;
}
div.productFrame{
    width:150px;
    display:inline-block;
    margin:10px;
    margin-bottom:0;
}
img.productPic{
    width:110px;
    height:110px;
    margin:20px;
    margin-bottom:5px;
    margin-top:10px;
    background:gray;
}
p.productName{
    text-align:center;
}
/*   以下是右边  */
/*              */
/*   ↖光↑↑翼↗   */
/*   ←( ·A·)→   */
/*   ↙展↓↓开↘   */
/*              */
// wfc你好骚鸭
div#productHot{
    @include border;
    @include rightFrame;
      background: rgba(255, 255, 255, 0.95);
}
</style>



