<template>
    <div id="x_comment">
        <div class="comment_top">
            <div class="comment_outline">
                <div><h4>我来说两句</h4>
                <div class="comment_data">（共
                <a>
                    <strong>0</strong>
                </a>条评论，<a> <strong>0</strong></a>
                人参与）</div></div>
                <div class="acount_right">
                  <div class="comment_exit">[退出]</div><div class="comment_account">用户xxx  | </div>
                </div>
            </div>
            <textarea placeholder="请输入评论" ></textarea>
            <button class="sendcomment" src="#">发表评论</button>
        </div>
        <div class="comment_bottom">
            <div class="comment_show_top">
            <ul class="comment_classify_btn">
                <li ><a @click="handleclick1" :class="[{border_red:isOn1}]">最新评论</a></li>
                <li ><a @click="handleclick2" :class="[{border_red:isOn2}]">热门评论</a></li>
            </ul>
            </div>
            <div class="comment_show_down"><p>暂无评论</p></div>
        </div>
    </div>
</template>

<script>
    export default {
        data(){
            return{
                isOn1:true,
                isOn2:false
            }
        },
    methods:{
        handleclick1:function(){
            this.isOn1=true
            this.isOn2=false
            console.log(1);
        },
         handleclick2:function(){
            this.isOn2=true
            this.isOn1=false
            console.log(1);
        }
    }
    
    }
</script>

<style lang="scss" scoped>
h4{
    font: bold 21px/30px 'Microsoft YaHei', '微软雅黑', SimHe, '黑体';
    float: left;
    color: #333;
    margin:0;
    padding:0;
}
.comment_top{
    
   textarea{
       resize:none;
        width:100%;
        height:100px;
        padding:0;
        top:0;
        outline:none;
        border: none;
        min-height: 100px;
        max-height: 100px;
        color: #666;
        
        font-size: 12px;
        border-bottom: solid 1px #ccc;
        outline: none;
        line-height: 20px;
        border: 1px solid #ccc !important;
    }
  
    button{
        color:white;
        background: 127de7;
        padding:5px 8px;
        border:none;
        box-shadow: 1px 1px 1px gray;
        font-size: 20px;
        float:right;
        width: 95px;
        height: 28px;
        padding: 0;
        color: #fff;
        font-size: 14px;
        font-weight: bold;
        line-height: 28px;
        text-align: center;
        border: 0;
        //background: url(http://images.ofweek.com/images/comment/comment_submit.jpg) center no-repeat;
        cursor: pointer;
    }
}
.acount_right{
  float:right;
}
  .comment_data{
    padding: 0 0 0 5px;
    color: #333;
    font-size:12px;
    a{
      line-height: 40px;
      text-align: left;
      font: 12px "瀹嬩綋";
      &:hover{
        text-decoration: underline;
      }
    }
  }
      strong{
        font-family: Verdana;
        font-style: italic;
        font-weight: normal;
        font-size: 17px;
        color: #cc0000 !important;
      }
  .comment_outline{
      background:#a49f9f2b;
  }
  .comment_outline{
      & div{
          display: inline-block;
          margin:5px 3px;
          vertical-align: top;
      }
      .comment_account{
          float:right;
          font-size: 14px;
          vertical-align: middle;
      }
  }
        .comment_exit{
          font-size: 14px;
          display: inline-block;
          vertical-align: middle;
          float:right;
          color:#0053ff;
          &:hover{
              color: #06c;
              text-decoration: underline;
          }
      }
      .comment_bottom{
          margin-top: 50px;
          width:100%;
      }
      .comment_show_top{
          //background:#a49f9f2b;
          display: block;
          width:100%;
          height:45px;
          line-height: 30px;
          ul{
            height:inherit;
            //height:30px;
            //line-height: 30px;
            width: 100%;
            height: 34px;
            line-height: 33px;
            border-bottom: 1px solid #ddd;
          }
          li{
              float:left;
              display: inline-block;
              height:inherit;
              line-height: inherit;
              padding:6px;
              //border:0.2px solid #d3d0d0;
              border-bottom:none;
              cursor: pointer;
              float: left;
              margin-right: 1em;
              font-family: microsoft yahei;
              font-size: 14px;
              a{

              }
          }
      }
      .border_red{
        border-bottom: 2px solid #cc0000;
        position: relative;
      }
      .comment_show_down{
        //border:0.6px gray solid;
        height: 100px;
        text-align: left;
      }
</style>


