<template>
  <div id="x_cahrtlist_single">
    <ul class="x_cahrtlist_single_title">
      <li v-for="(item,key_1) in morelist.list" :key="key_1" class="x_cahrtlist_single_singleone" >
        <div class="right_part">
          <div @click="routerLink(morelist.list[key_1].id,items)" class="Title_hover">
            <i class="fa fa-flag fa-2x"></i>
            <h2 class="title">
              <a>
                {{item.title}}
              </a>
            </h2>
          </div>
          <div class="summary">
            <p class="pre_view" >
            {{item.readGuide}}
            </p>
          </div>
          <div class="right_part_bottom">
            <div class="sort_type">
              <div>
              {{item.source}} |
              </div>
            </div>
            <div class="time" >
              <div class="time_tag">
                 时间：
              </div>
              <div>
              {{item.postDate}}
              </div>
            </div>
          </div>
        </div>
      </li>
    </ul>

  </div>
</template>

<script>


export default {
  name: "charlist_single",
  props:{
    morelist:{
     
      requi127de7:true
    },
    url_last:{
      type:String,
      requi127de7:true
    },

  },
  data() {
    return {
      items:[
        {
          list:"协会",
          name:"协会动态",
          newsUrl:"information/getAssociationNewsInfo"
        },
        {
          list:"协会",
          name:"通知公告",
          newsUrl:"information/getNoticeInfo"
        },
        {
          list:"资讯",
          name:"行业动态",
          newsUrl:"/information/findInformationInfo" 
        },
        {
          list:"资讯",
          name:"政策",
          newsUrl:"/information/findPolicyInfo" 
        },
      ],
      key_3:{
        key:0
        }
        
      
    };
  },
  
  components: {
    
  },
  methods: {
   routerLink(messageId, item) {
        var pathing = "/association/industry_dynamic_text";
        if(this.url_last=='/information/getAssociationNewsList'){
          this.$set(this.key_3, "key", 0);
        }else if(this.url_last=='/information/getNoticeList'){
          this.$set(this.key_3, "key", 1);  //this.key_3.key=1;这个也行？
          
        }else if(this.url_last=="/information/findInformationList"){
          this.$set(this.key_3, "key", 2);
          //item[this.key_3.key].newsUrl =  item[this.key_3.key].newsUrl + messageId;
        }else if(this.url_last=="/information/findPolicyList"){
          this.$set(this.key_3, "key", 3);
          //item[this.key_3.key].newsUrl =  item[this.key_3.key].newsUrl + messageId;
          console.log(item[this.key_3.key].newsUrl);
          pathing = "/policy/policy_text"
        }
        item = JSON.stringify(item[this.key_3.key]);
        
        this.$router.push({
          path: pathing,
          query: { messageId: messageId, item: item }
        });
      
    }
}
}
</script>

<style lang="scss" scoped>
.title:hover>a{
  color:red;
}
.x_cahrtlist_single_singleone{
      border-bottom: dashed 1px #ccc;
      box-shadow: 0 0 1px #bebebe;
      &:hover{
        background-color:#f6f6f6;
      }
}
*{
  //color: #3f3f3f;
  letter-spacing: 1px;
  
}
  .right_part { 
    width: 100%;
    height:135px;
    display: inline-block;
    padding: 5px;
    text-overflow: ellipsis;
    white-space: nowrap;

  }
  .pre_view{
    white-space: nowrap;
    text-overflow: ellipsis;
    width:700px;
    overflow: hidden;
  }
  #x_cahrtlist_single{
    padding:3px;
  }
  i{
    color:127de7;
    vertical-align: top;
    margin-top:12px;
    color:red;
  }
  i,h2{
    display: inline-block;
  }
  h2{
    font-size: 18px;
    font-weight: bold;
    line-height: 24px;
    height: 24px;
    margin-bottom: 4px;
    display: inline-block;
  }
  h2 a{
    color: #3d3d3d;
    display: block;
    line-height: 24px;
    height: 24px;
    word-wrap: break-word;
    word-break: break-all;
    overflow: hidden;
    text-overflow: ellipsis;
    width:680px;
    &:hover{
      color:127de7;
      cursor: pointer;
    }
  }
  .summary{
    margin-bottom: 4px;
    color: #8a8a8a;
    font-size: 14px;
    line-height: 24px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .right_part_bottom{
    height:50px;
  }
  .right_part_bottom div{
    display:inline-block;
    float: left;
    font-size: 14px;
    color: #8a8a8a;
    line-height: 24px;
    word-break: break-all;
    padding: 5px;
  }
  .sort_type,time{
    display: inline-block;
    float:left;
  }
  .title{  //只有设在最近的才有用
    width:730px;
    font-size: 20px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    font-family: "\5FAE\8F6F\96C5\9ED1", "Microsoft Yahei", Verdana, sans-serif;

  }
  .sort_type{
    //border-right: solid 1px #4e4e4e;
  }
  .time{
    padding:5px;
    div{
      display:inline-block;
      float: left;
      font-size: 14px;
      color: #8a8a8a;
      line-height: 24px;
      word-break: break-all;
    }
  }

</style>


