<template>
  <div id="talent-default">
    <div class="left_block">
      <!-- 位置路径 -->
      <div class="box1">
        <div class="curr_position">
          当前位置：
          <a @click="selectItem('/home')">服务平台</a>
          <span>></span>
          <a @click="selectItem('/talent')">人才</a>
        </div>
      </div>
      <!-- 职位搜索 -->
      <div class="box2">
        <ul class="title">
          <li class="list_title">
            <a class="active" style="margin-left:15px;">职位搜索</a>
          </li>
        </ul>
        <div style="width:100%;">
          <keep-alive>
            <job-search></job-search>
          </keep-alive>
        </div>
      </div>
      <!-- 最新招聘 -->
      <div class="box3">
        <ul class="title">
          <li class="list_title">
            <a class="active" style="margin-left:15px;">最新招聘</a>
          </li>
          <span class="more" @click="selectItem('/talent/talent-job')">-更多></span>
        </ul>
        <div style="height:270px;width:100%;">
          <keep-alive>
            <job-list v-on="$listeners"></job-list>
          </keep-alive>
        </div>
      </div>
      <!-- 专家库 -->
      <div class="box3">
        <ul class="title">
          <li class="list_title">
            <a class="active" style="margin-left:15px;" @click="selectItem(tabs[6].path,tabs[6])">专家库</a>
          </li>
          <span class="more" @click="selectItem(tabs[6].path,tabs[6])">-更多></span>
        </ul>
        <div style="height:170px;width:100%;">
          <keep-alive>
            <pen-list :my-message="tabMessage[0]" :current-tab="currentTab[0]" :isRequested="this.isRequested"></pen-list>
          </keep-alive>
        </div>
      </div>
      <!-- 高校 -->
      <div class="box3">
        <ul class="title">
          <li class="list_title">
            <a class="active" style="margin-left:15px;" @click="selectItem(tabs[7].path,tabs[7])">高校</a>
          </li>
          <span class="more" @click="selectItem(tabs[7].path,tabs[7])">-更多></span>
        </ul>
        <div style="height:170px;width:100%;">
          <keep-alive>
            <pen-list :my-message="tabMessage[1]" :current-tab="currentTab[1]" :isRequested="this.isRequested"></pen-list>
          </keep-alive>
        </div>
      </div>
      <!-- 专家智点 -->
      <div class="box3">
        <ul class="title">
          <li class="list_title">
            <a class="active" style="margin-left:15px;" @click="selectItem(tabs[8].path,tabs[8])">专家智点</a>
          </li>
          <span class="more" @click="selectItem(tabs[8].path,tabs[8])">-更多></span>
        </ul>
        <div style="height:270px;width:100%;">
          <keep-alive>
            <ul-list :my-message="tabMessage[2]" :current-tab="currentTab[2]" :isRequested="this.isRequested"></ul-list>
          </keep-alive>
        </div>
      </div>
    </div>
    <div class="right_block">
      <!-- 广告招收 -->
      <div class="box4">
        <ul class="title">
          <li class="list_title">
            <a style="margin-left:15px;" class="active">广告招收栏目</a>
          </li>
        </ul>
        <div style="height:270px;width:100%;">
          <keep-alive>
            <!-- <advertise-list></advertise-list> -->
          </keep-alive>
        </div>
      </div>
      <!-- 用户 -->
      <div class="box4">
        <ul class="title" style="margin-left:15px;">
          <li class="list_title" v-for="(tab,index) in tabs" v-if="tab.belong === '用户'" :key="tab.name" @mouseover="tabLink(index,tab.tocpn)">
            <a :class="{active:tab.isActive}">{{tab.name}}</a>
          </li>
        </ul>
        <div style="height:270px;width:100%;">
          <keep-alive>
            <pb-list :is="currentTab[3].cpn"></pb-list>
          </keep-alive>
        </div>
      </div>
      <!-- 活动 -->
      <div class="box4">
        <ul class="title" style="margin-left:15px;">
          <li class="list_title" v-for="(tab,index) in tabs" v-if="tab.belong === '活动'" :key="tab.name" @mouseover="tabLink(index,tab.tocpn)">
            <a :class="{active:tab.isActive}">{{tab.name}}</a>
          </li>
        </ul>
        <div style="height:270px;width:100%;">
          <keep-alive>
            <pb-list :is="currentTab[4].cpn"></pb-list>
          </keep-alive>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import MHeader from "@/components/common/m-header";
import JobList from "@/components/common/job-list";
import NormalList from "@/components/common/normal-list";
import PenList from "@/components/common/pen-list";
import UlList from "@/components/common/ul-list";
import JobSearch from "@/components/common/job-search";

export default {
  name: "talent-default",
  data() {
    return {
      //标志性内容，传给子组件做导航判断请求接口判断
      currentTab: [
        {
          name: "人才",
          list: "专家库",
          cpn: "pen-list"
        },
        {
          name: "人才",
          list: "高校",
          cpn: "pen-list"
        },
        {
          name: "人才",
          list: "专家智点",
          cpn: "ul-list"
        },
        {
          name: "用户",
          list: "",
          cpn: "normal-list",
          newsUrl: ""
        },
        {
          name: "活动",
          list: "",
          cpn: "normal-list",
          newsUrl: ""
        }
      ],
      //各小标题块内容,传给子组件渲染页面内容
      tabMessage: ["", "", "", "", ""],
      tabs: [
        {
          name: "用户中心",
          belong: "用户", //归属的小标题
          path: null, //点击跳转
          tocpn: "", //子组件名
          content: null, //组件内容，对象数组
          isActive: true
        },
        {
          name: "订阅资讯",
          belong: "用户", //归属的小标题
          path: null, //点击跳转
          tocpn: "normal-list", //子组件名
          content: null, //组件内容，对象数组
          isActive: false
        },
        {
          name: "我的交易",
          belong: "用户", //归属的小标题
          path: null, //点击跳转
          tocpn: "normal-list", //子组件名
          content: null, //组件内容，对象数组
          isActive: false
        },
        {
          name: "近期活动",
          belong: "活动", //归属的小标题
          path: null, //点击跳转
          tocpn: "", //子组件名
          content: null, //组件内容，对象数组
          isActive: true
        },
        {
          name: "协会活动",
          belong: "活动", //归属的小标题
          path: null, //点击跳转
          tocpn: "", //子组件名
          content: null, //组件内容，对象数组
          isActive: false
        },
        {
          name: "其他",
          belong: "活动", //归属的小标题
          path: null, //点击跳转
          tocpn: "", //子组件名
          content: null, //组件内容，对象数组
          isActive: false
        },
        {
          name: "专家库",
          belong: "人才", //归属的小标题
          path: "/talent/talent-list", //点击跳转
          tocpn: "pen-list", //子组件名
          message: null, //组件内容，对象数组
          isActive: true
        },
        {
          name: "高校",
          belong: "人才", //归属的小标题
          path: "/talent/talent-list", //点击跳转
          tocpn: "pen-list", //子组件名
          message: null, //组件内容，对象数组
          isActive: false
        },
        {
          name: "专家智点",
          belong: "人才", //归属的小标题
          path: "/talent/talent_experience", //点击跳转
          tocpn: "ul-list", //子组件名
          message: null, //组件内容，对象数组
          isActive: false,
          requestPath: "/expert/getExpertArtByPage", //文章列表url
          newsUrl: "/expert/findExpertArtInf" //具体文章请求url
        }
      ],
      //子组件开始渲染的标志
      isRequested: false
    };
  },
  methods: {
    selectItem(path, item) {
      this.$emit("select", path, item);
    }, //将item通过select传给父组件
    // routerLink(path) {
    //   this.$router.push(path); //路由跳转
    // },
    tabLink(index, tocpn) {
      //更改高亮标签
      this.$set(
        this.tabs,
        this.tabs[index],
        (this.tabs[index].isActive = true)
      );
      for (var i = 0; i < this.tabs.length; i++) {
        if (this.tabs[i].belong == this.tabs[index].belong && i != index) {
          this.$set(this.tabs, this.tabs[i], (this.tabs[i].isActive = false)); //解决methods中无法更改data数据的问题
        }
      }
      var t = 3;
      if (this.tabs[index].belong == "用户") t = 3;
      else if (this.tabs[index].belong == "活动") t = 4;
      //更改小标题块内容
      this.currentTab[t].cpn = this.tabs[index].tocpn;
      this.currentTab[t].list = this.tabs[index].name;
      this.currentTab[t].newsUrl = this.tabs[index].newsUrl;
      this.isRequested = this.isRequested == true ? false : true; //同组件不同tab切换监听
    },
    init() {
      var url = this.HOST + "/common/getIndex";
      this.axios
        .get(url)
        .then(response => {
          console.log(response.data.result);
          //页面信息初始化
          this.tabMessage[0] = response.data.message.experts;
          this.tabMessage[1] = response.data.message.school;
          this.tabMessage[2] = response.data.message.expertArts;

          this.isRequested = true; //组件初始化监听
        })
        .catch(function(response) {
          console.log(response);
        });
    }
  },
  components: {
    MHeader,
    JobList,
    NormalList,
    PenList,
    UlList,
    JobSearch
  },
  activated() {
    this.init();
  }
};
</script>

<style lang="scss" scoped>
@mixin left {
  margin: 5px auto;
  width: 795px;
  float: left;
  box-sizing: border-box;
  overflow: hidden;
}
@mixin right {
  margin: 5px auto;
  width: 390px;
  float: right;
  box-sizing: border-box;
  overflow: hidden;
}
@mixin i-box {
  -webkit-box-shadow: 0 0 1px #bebebe;
  box-shadow: 0 0 1px #bebebe;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border: 1px solid rgba(204, 204, 204, 0.377);
  height: 100%;
  overflow: hidden;
  box-sizing: border-box;
  background: rgba(255, 255, 255, 0.95);
}
.active {
  color: #127de7 !important;
  border-bottom: 2px solid #127de7;
  padding-bottom: 4px;
}
#talent-default {
  margin: 5px auto;
  width: 1200px;
  overflow: hidden;
}
.left_block {
  @include left;
}
.right_block {
  @include right;
}
.box1,
.box2,
.box3 {
  @include left;
  @include i-box;
}
.box1 {
  & {
    color: #404040;
    font-weight: 500;
    padding: 0 15px;
    overflow: hidden;
  }
  .curr_position {
    & {
      margin-top: 14px;
      margin-bottom: 15px;
    }
    a {
      text-decoration: none;
      cursor: pointer;
    }
  }
}
.box4 {
  @include right;
  @include i-box;
}
.title {
  margin-bottom: 12px;
  margin-top: 2px;
  width: 100%;
  height: 28px;
  line-height: 28px;
}
.title li {
  float: left;
}
.more {
  & {
    color: #666;
    font-size: 14px;
    float: right;
    padding: 2px 10px 0 2px;
    cursor: pointer;
  }
  &:hover {
    color: #127de7;
  }
}
.list_title a {
  color: #7b7b7b;
  cursor: default;
  font-size: 1em;
  font-weight: 500;
  line-height: 25px;
  vertical-align: middle;
  margin-right: 10px;
}
.list_title a:hover {
  color: #127de7;
  border-bottom: 2px solid #127de7;
  cursor: default;
  padding-bottom: 4px;
}
</style>




