<template>
  <div id="x_singlesidebar">
      <div class="x_singlesidebar">
          <div class="x_singlesiderbar_title"><a>{{tab_three1.name}}</a></div>
          <span class="x_singlesiderbar_title_more" @click="routerLink(tab_three1.name,-1)"><a>更多</a></span>
      </div>
      <div class="x_singlesiderbar_content">
          <h3 class="x_singlesiderbar_title_list_first" @click="routerLink(tab_three1.name,list.message[0].id)"><a>{{this.list.message[0].title}}</a></h3>
          <li v-for="(li,index) of list.message" :key="index" class="x_singlesiderbar_li" v-if="index >= 1&& index <7" @click="routerLink(tab_three1.name,list.message[index].id)"><a>{{li.title}}</a><a></a></li>
      </div>
  </div>
</template>

<script>


export default {
        props: {
        tab_three1: {
          
        },
        isRequested: {
            type:Boolean,
            required:true
        }
    },
    watch:{  //保证数据再刷新的时候不会丢失
        isRequested(){
            if(this.tab_three1.message){
                this.list = this.tab_three1;
            } 
        },
    } ,   
    data(){
        return {
            title:"广告招商",
            list_first:"世界机器人大会背后的中国制造",
            list:{name:"此处应有标题",
            message:[{title:"数据正在赶来啦。。。"},
            {title:"数据正在赶来啦。。。"},
            {title:"数据正在赶来啦。。。"},
            {title:"数据正在赶来啦。。。"},
            {title:"数据正在赶来啦。。。"},
            {title:"数据正在赶来啦。。。"},
            {title:"数据正在赶来啦。。。"},
            {title:"数据正在赶来啦。。。"},
            ]}
            

        }
    },
    methods:{
        init(){
            if(this.tab_three1.message){  //这样就算没有信息也可以显示原有信息
                this.list = this.tab_three1;
            } 
        },
        routerLink(name,id){
            let path;
            let url;
            let item ={
                newsUrl:"",
                list:"",
                name:"",
                requestPath:"",
                belong:"",
            }
            if(id!=-1){
                switch(name){
                case "政策": url = "/information/findPolicyInfo";path ="/policy/industry_dynamic_text";item.name ="政策";item.requestPath="/information/findPolicyList";break;  //政策
                case "行业动态": url = "/information/findInformationInfo";path ="/news/industry_dynamic_text";item.name ="行业动态";item.requestPath="/information/findInformationList";break;  //行业动态
                case "行业报告": url = "/information/findReportInfo";path ="/news/industry_dynamic_text";item.name ="行业报告";item.requestPath="/information/findReportByPage";break;  //行业报告
            }
            }
            if(id==-1){
                switch(name){
                case "政策": url = "/information/findPolicyInfo";path ="/policy";item.name ="政策";item.requestPath="/information/findPolicyList";break;  //政策
                case "行业动态": url = "/information/findInformationInfo";path ="/news/industry_dynamic";item.name ="行业动态";item.requestPath="/information/findInformationList";break;  //行业动态
                case "行业报告": url = "/information/findReportInfo";path ="/news/industry_dynamic";item.name ="行业报告";item.requestPath="/information/findReportByPage";break;  //行业报告
            }
            }
            item.newsUrl=url;
            item = JSON.stringify(item);
            this.$router.push({
                path:path,
                query:{
                    item:item,
                    messageId: id
                }
            })

        }
    },
    activated(){
         this.init()
    }, 
/*       mounted() {
    this.init();
    }    */
}
</script>

<style lang="scss" scoped>
#x_singlesidebar{
    height:inherit;
}
*{
    font: 13px/22px "微软雅黑";
}
.x_singlesiderbar_title_list_first a{
    font-size: 16px;
    width: 500px;
    height: 20px;
    padding-left: 15px;
    display: block;
    margin-top: 15px;
    font-weight: bold;
    color: #3f3f3f;
    cursor: pointer;
    text-overflow: ellipsis;
    margin-bottom:15px;
    overflow: hidden;
    white-space: nowrap;
}
    .x_singlesidebar{
        color: #3f3f3f;
        background: #FFF;
        height: 31px;
        display: block;
        width: 100%;
        padding: 5px 0px;
    }
    .x_singlesiderbar_title{
        float: left;
        line-height: 31px;
        
    }
    .x_singlesiderbar_title_more{
        display: inline-block;
        float:right;
    }
    .x_singlesiderbar_title_more a{
    font-size: 14px;
    float: right;
    height: 31px;
    line-height: 31px;
    display: inline-block;
    margin-right: 15px;
    padding-left: 20px;
    }
     .x_singlesiderbar_title_more :before {
    content: "+";
    display: inline-block;
    color: #127de7;
    text-align: center;
    vertical-align: top;
    position: static;
    font-size: 20px;
    width: 20px;
    height: 29px;
    line-height: 27px;
    /* background: url(../images/picxi.png) no-repeat -3px -586px; 图片旋转*/
    transition: all 0.2s;
    -webkit-transition: all 0.2s;
    -moz-transition: all 0.2s;
    -ms-transition: all 0.2s;
    -o-transition: all 0.2s;
    transition: all 0.2s;
}
    .x_singlesiderbar_title_more a{
            cursor: pointer;

    }
    .x_singlesiderbar_title a{
    color: #454545;
    text-align: center;
    height: 30px;
    line-height: 30px;
    display: block;
    padding: 0 15px;
    margin: 0 !important;
     background: #FFF;
    color: #127de7 !important;
     font-weight: bold;
     font-size: 16px;
    }
    .x_singlesiderbar_content{
        min-height:200px;
       
    }
    .x_singlesiderbar_li{
        font-weight: bold;
        width: auto;
        padding: 0 15px;
        height: 20px;
        font-size: 13px;
        padding:15px;
        margin: 0 !important;
        border-top: 1px solid #dedede;
        padding: 3px 0;
        a{overflow: hidden;
        padding-left:10px;
        text-overflow: ellipsis;
        white-space: nowrap;}

       a{
           padding-left:15px;
       }
        &:hover{
            color:#e06a6a;
            cursor: pointer;

        }

    }

</style>




