                coverImg: null,
                postDate: "2014-12-01",
                viewCount: 0,
                type: 0
              }
            ]
          }
        }
      ]
    };
  },
  methods: {
    routerLink(path) {
      this.$router.push(path); //路由跳转
    }
  },
  components: {}
};
</script>

