<template>
 <div id="x_hotsearch">
  <!-- <m-header></m-header> -->
  <div class="hotsearch-leftpart">
   <div class="hotsearch-remind">热门搜索：</div>
   <ul class="hotsearch-recommend">
    <li v-for="(item,index) of List  ">{{item}}</li>
   </ul>
    </div>
  <div class="hotsearch-rightpart">
    <x_hotsearch2></x_hotsearch2>
<!--    <li class="hotsearch-right-leftpart">
    <div>
     <div class="x-downlist-outline">
      <div class="x-downlist-all">全部</div>
      <div class="x-downlist" @click="handleclick"><img src="../../assets/seach-bot.jpg"></div>
     </div>
     <ul class="hotsearching-choose-list" v-if="show">
 
 
 
      <li v-for="(item,index) of List">{{item}}</li>
 
 
 
     </ul>
     <label>
    <input >
    </label>
    </div>
    </li>
    <li class="hotsearch-right-rightpart">
    <input value="搜索">
   </li> -->
  </div>
 </div>
</template>

<script>
 import x_hotsearch2 from "@/components/common/hot-search2.vue";
 export default {
  data() {
   return {
    List: ['产品推荐', '行业报告', '技术标准', '政策'],
    show: false
   }
  },
 
 
 
  methods: {
 
 
 
   handleclick: function() {
 
 
 
    this.show = !this.show;
 
 
 
   }
  },components:{
    x_hotsearch2
  }
 }
</script>

<style lang="scss" scoped>
 #x-hotsearch {
 
  width: 100%;
 
  height: 100%;
 
  padding: 15px;
  
 
 }
 
 
 
 .hotsearch-recommend {
  display: inline-block;
  margin: 0;
  padding: 0;
  z-index: 30;
  li {
   display: inline-block;
   float: right;
   padding: 0 10px;
   z-index: 30;
   &:hover {
    color: #127de7;
    cursor: pointer;
   }
  }
 
 }
 .hotsearch-leftpart{
   margin:0 auto;
   text-align: center;
 }
 
 
 
 .hotsearching-choose-list li {
 
 
  &:hover {
 
   color: #127de7
 
  }
 
 }
 
 
 
 .hotsearching-choose-list li {
 
  z-index: 30;
 
  background: white;
 
  &:hover {
 
   color: #127de7;
 
   cursor: pointer;
 
  }
 
 }
 
 
 
 .hotsearch-remind {
 
  display: inline-block;
 
  vertical-align: top;
 
 }
 
 
 
 .x-downlist-outline {
 
  display: inline-block;
  line-height: inherit;
  height: inherit;
 }
 .x-downlist {
  display: inline-block;
  height: 20px;
  //display: inline-block;//span一定要设这个
  img {
 
   height: 20px;
 
   width: 40px;
 
  }
 
 }
 
 
 
 .hotsearching-choose-list {
 
  position: absolute;
 
  border: 1px solid #c7c7c7;
  border-bottom:none;
  border-top:none;
 
  li {
 
   padding: 3px;
 
   text-align: center;
  
 
  }
 
 }
 
 
 
 .x-downlist-all {
 
  display: inline-block;
 
  vertical-align: top;
     margin:0 auto;
    text-align: center;
 
  &:hover {
 
   color: #127de7;
    cursor: pointer;
 
  }
 
 }
 .hotsearch-leftpart{
   margin-top:8px;
 }
 
 .hotsearch-leftpart,
 
 .hotsearch-rightpart,
 
 .hotsearch-right-leftpart,
 
 .hotsearch-right-rightpart {
 
  display: inline-block;
 
 }
 .hotsearch-right-leftpart {
  border-right: #127de7 2px solid;
  float: left;
 }
 .hotsearch-right-leftpart input {
  height: 19.5px;
  vertical-align: top;
 }
 .hotsearch-right-rightpart {
 
  padding: 2px;
 
  background-color: #127de7;
 
 }
 
 
 
 .hotsearch-right-rightpart input {
 
  background-color: #127de7;
 
  width: 50px;
 
  text-align: center;
  border: solid 1px #127de7;
  color: white;
 }
 .hotsearch-rightpart {
   vertical-align: top;
   top:0;
  //float: right;
  margin-left:350px;
  
 }
</style>



