<template>
  <div id="paging">
    <button class="home_page" @click="first();init();" :class="[{pagingcurrent: pageIndex === 1}]">首页</button>
    <button class="Previous_page" @click="prev();init();" :class="[{pagingcurrent: pageIndex === 1}]">上一页</button>
    <ul class="Numeric_list">
      <li v-for="item1 of list1" @click="go(item1);init();" :class="[{isshow:isOn1,pagingcurrent: pageIndex == item1}]">{{item1}}</li>
      <p v-if="this.pagecount>8" class="Ellipsis">...</p>
      <li v-for="item2 of list2" @click="go(item2);init();" :class="[{pagingcurrent: pageIndex == item2}]"><a sec="#">{{item2}}</a></li>
      <p v-if="this.pagecount>8" class="Ellipsis">...</p>
      <li v-for="item3 of list3" @click="go(item3);init();" :class="[{isshow:isOn2,pagingcurrent:pageIndex == item3}]">{{item3}}</li>
    </ul>
    <button class="next_page" @click="next();init();" :class="[{pagingcurrent: pageIndex === pagecount}]">下一页</button>
    <button class="last_page" @click="last();init();" :class="[{pagingcurrent: pageIndex === pagecount}]">末页</button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      pagecount: 1, //模拟一下父组件传值的页码总数
      pageIndex: 2, //当前页面
      pagecenter: 5,
      isOn1: false,
      isOn2: false,
      myFun:{
        key:false,
        
      }
    };
  },
  props:{
    pagenumber:{
      type:Object,
      required:true
    },
    url_last:{
      type:String,
      required:true
    }
  },
  computed: {
    list2: function() {
      this.pagecount = this.pagenumber.pages;
      this.pageIndex = this.pagenumber.pageNum;
      //一开始初始化计算要生成多少个小的li
      var list2 = [];
      if (this.pagecount >= 9) {
        for (var i = this.pagecenter - 2; i <= this.pagecenter + 2; i++) {
          list2.push(i);
        }
        return list2;
      }
      if (this.pagecount >= 5) {
        this.pagecenter = this.pagecount;
        for (var i = 3; i <= this.pagecount - 2; i++) {
          list2.push(i);
        }
        return list2;
      }
      if (this.pagecount < 5) {
        for (var i = 1; i <= this.pagecount; i++) {
          list2.push(i);
        }
        return list2;
      }
    },
    list1: function() {
      var list1 = [];
      if (this.pagecount >= 5) {
        for (var i = 1; i <= 2; i++) {
          list1.push(i);
        }
        return list1;
      } else {
        return list1;
        ison1: true;
      }
    },
    list3: function() {
      var list3 = [];
      if (this.pagecount >= 5) {
        for (var i = this.pagecount - 1; i <= this.pagecount; i++) {
          list3.push(i);
        }
        return list3;
      } else {
        return list3;
        ison2: true;
      }
    }
  },
  methods: {
    init(){
      
        var qs = require('qs');
          console.log(this.pageIndex);

        this.axios.get(this.HOST+this.url_last, {
          params: {
            pageNum:this.pageIndex
          }
        })
        .then(response => {
          console.log(response.data.message);
          this.$emit('backnumber',response.data.message);
          })
        .catch(function(response) {
          console.log(response);
              
        });
      
  },
    prev() {
        if(this.pageIndex > 1){
          if (this.pageIndex < this.pagecount - 4 && this.pageIndex >= 6) {
            this.pageIndex--;
            this.pagecenter = this.pageIndex;
          } else if (this.pageIndex < 6 || this.pageIndex > this.pagecount - 4) {
            this.pageIndex--;
          }
        }
    },
    next() {
        if(this.pageIndex < this.pagecount){
          if (this.pageIndex < this.pagecount - 4 && this.pageIndex >= 6) {
            this.pageIndex++;
            this.pagecenter = this.pageIndex;
          } else if (this.pageIndex < 6 || this.pageIndex > this.pagecount - 4) {
            this.pageIndex++;
          }
        }
    },
    go(pager) {
        console.log(pager);
        this.pageIndex = pager;
        if (pager > this.pagecount - 4) {
          this.pageIndex = pager;
          this.pagecenter = this.pagecount - 4;
          console.log(this.pageIndex);
        }
        if (pager < 6) {
          this.pageIndex = pager;
          this.pagecenter = 5;
          console.log(this.pageIndex);
        }
        if (pager < this.pagecount - 4 && pager >= 6) {
          this.pageIndex = pager;
          this.pagecenter = this.pageIndex;
          console.log(this.pageIndex);
        }
        console.log(this.pageIndex);
    },
    first: function() {
        this.pageIndex = 1;
        this.pagecenter = 5;
    },
    last: function() {
        this.pageIndex = this.pagecount;
        this.pagecenter = this.pagecount - 4;
      }
    
  },
  watch: {
    pageIndex(val) {}
  }
};
</script>

<style lang="scss" scoped>
#paging {
  text-align: center;
  div {
    display: inline-block;
  }
  ul {
    display: inline-block;
  }
  div,button,
  ul li {
    text-decoration: none;
    background: #fff;
    border-radius: 3px;
    padding: 5px 8px;
    border: 1px solid #bebebe;
    &:hover {
      border: 1px solid #127de7;
      color: #127de7;
      cursor: pointer;
    }
  }
  a {
    outline: none;
  }
  button{
    padding:5.5px 8px;  //手动对齐(捂脸)
    outline:none;
  }
}
.Previous_page {
}
.Numeric_list {
  li {
    display: inline-block;
    padding: 5px 10px;
    margin: 0 4px;
  }
}
.home_page {
}
.isshow {
  display: none;
}
.pagingcurrent {
  color: #e1e1dc!important;
}
.Ellipsis{
  display: inline-block;
  color:white;
}
</style>



