<template>
  <div id="ul-list">
    <ul>
      <li v-for="(item, index) in items" v-if="">
        <a class="list_title" @click="routerLink(item.id,currentTab)"> {{item.title}} </a>
        <b>{{item.postDate}}</b>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  name: "ul-list",
  props: ["myMessage", "isRequested", "currentTab"],
   watch: {
    isRequested() {
      this.init();
    }
  }, 
  methods: {
    init() {
      if (this.myMessage) {
        if (
          this.currentTab.name == "资讯" ||
          this.currentTab.name == "协会" ||
          this.currentTab.name == "产品"
        ) {
          this.items = this.myMessage.slice(4,12);
        } else {
          this.items = this.myMessage.slice(0,8);
        }
        // console.log(this.items);
      }
    },
    routerLink(messageId, item) {
      let data = JSON.stringify(item);
      if (this.myMessage) {
        // console.log(messageId);

        let url = "/news/industry_dynamic_text";
        if (this.currentTab.name == "资讯") {
          url = "/news/industry_dynamic_text";
        }
        if (this.currentTab.name == "协会") {
          url = "/association/industry_dynamic_text";
        }
        if (this.currentTab.name == "产品") {
          url = "/product/industry_dynamic_text";
        }
        if (this.currentTab.name == "人才") {
          url = "/talent/talent_experience_text";
        }
        if (this.currentTab.list == "政策") {
          url = "/policy/policy_text";
        }
        this.$router.push({
          path: url,
          query: { messageId: messageId, item: data }
        });
      }
    }
  },
  data() {
    return {
      items: [
        {
          title: "数据正在赶来的路上......",
          postDate: "2018-09-20"
        },
        {
          title: "数据正在赶来的路上......",
          postDate: "2018-09-20"
        },
        {
          title: "数据正在赶来的路上......",
          postDate: "2018-09-20"
        },
        {
          title: "数据正在赶来的路上......",
          postDate: "2018-09-20"
        },
        {
          title: "数据正在赶来的路上......",
          postDate: "2018-09-20"
        },
        {
          title: "数据正在赶来的路上......",
          postDate: "2018-09-20"
        },
        {
          title: "数据正在赶来的路上......",
          postDate: "2018-09-20"
        },
        {
          title: "数据正在赶来的路上......",
          postDate: "2018-09-20"
        }
      ]
    };
  },
  components: {},
  activated() {
    this.init();
    // console.log(this.myMessage);
  },
  mounted() {
    this.init();
  }
};
</script>
<style lang="scss" scoped>
* {
  margin: 0;
  padding: 0;
  list-style: none;
}
@mixin whole {
  width: 100%;
  height: 100%;
}
#ul-list {
  @include whole;
}
#ul-list ul {
  list-style-type: none;
  padding: 0 15px;
  text-align: left;
}
#ul-list ul li {
  background: none;
  padding: 0;
  margin: 0;
  border-top: none;
  line-height: 15px;
  padding: 8px 0 7px;
  font-size: 13px;
  clear: both;
}
.list_title {
  color: #3f3f3f;
  display: inline-block;
  max-width: 70%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  line-height: 15px;
  vertical-align: middle;
  cursor: pointer;
}
.list_title:hover {
  color: #127de7;
}
#ul-list ul li b {
  float: right;
  padding: 0 5px;
  font-weight: 100;
  display: block;
  height: 15px;
  overflow: hidden;
  font-family: Verdana;
}
</style>



