<template>
  <div id="pb-list2">
    <div class="pics">
      <div v-for="(item, index) in items" v-if="index<2">
        <img :src="item.img==null?'./static/PICS/pic3.png':item.img" alt="">
        <p :title="item.title" @click="routerLink(item.id,currentTab)">{{item.title}}</p>
      </div>
    </div>
    <ul>
      <li v-for="(item, index) in items" v-if="index<9 && index>1">
        <a class="list_title" :class="{ big: item.isBig }" @click="routerLink(item.id,currentTab)"> {{item.title}} </a>
        <b>{{item.postDate}}</b>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  name: "pb-list2",
  props: ["myMessage", "currentTab", "isRequested"],
  watch: {  //讲真这个是没有用的
    isRequested() {
      this.init();
    }
  },
  data() {
    return {
      items: [
        {
          title: "数据内容正在赶来的路上......",
          postDate: "2018-09-20",
          isBig: true  //判断要不要显示红色
        },
        {
          title: "数据内容正在赶来的路上......",
          postDate: "2018-09-20",
          isBig: false
        },
        {
          title: "数据内容正在赶来的路上......",
          postDate: "2018-09-20",
          isBig: false
        },
        {
          title: "数据内容正在赶来的路上......",
          postDate: "2018-09-20",
          isBig: false
        },
        {
          title: "数据内容正在赶来的路上......",
          postDate: "2018-09-20",
          isBig: false
        },
        {
          title: "数据内容正在赶来的路上.........",
          postDate: "2018-09-20",
          isBig: false
        },
        {
          title: "数据内容正在赶来的路上......",
          postDate: "2018-09-20",
          isBig: false
        },
        {
          title: "数据内容正在赶来的路上......",
          postDate: "2018-09-20",
          isBig: false
        },
        {
          title: "数据内容正在赶来的路上......",
          postDate: "2018-09-20",
          isBig: false
        }
      ]
    };
  },
  methods: {
    init() {
      if (this.myMessage) {   //这个就是没数据是显示什么
        // console.log(this.myMessage);
        this.items = this.myMessage;
        // console.log(this.items);
      }
    },
    routerLink(messageId, item) {  //判断到底是准备跳转之后发什么请求
      let data = JSON.stringify(item);
      if (this.myMessage) {
        console.log(this.currentTab.list);

        let url = "/news/industry_dynamic_text";
        if (this.currentTab.name == "资讯") {
          url = "/news/industry_dynamic_text";
        }
        if (this.currentTab.name == "协会") {
          url = "/association/industry_dynamic_text";
        }
        if (this.currentTab.name == "产品") {
          url = "/product/industry_dynamic_text";
        }
        if (this.currentTab.name == "人才") {
          url = "/talent/talent_experience_text";
        }
        if (this.currentTab.list == "政策") {
          url = "/policy/policy_text";
        }
        if (this.currentTab.name == "企业") {
          url = "/enterprise/industry_dynamic_text";
        }
        this.$router.push({
          path: url,
          query: { messageId: messageId, item: data }
        });
      }
    }
  },
  activated() {
    this.init();
  },
  mounted() {
    this.init();
  }
};
</script>
<style lang="scss" scoped>
* {
  margin: 0;
  padding: 0;
  list-style: none;
}
@mixin whole {
  width: 100%;
  height: 100%;
}
#pb-list2 {
  @include whole;
  border-bottom: #ccc thin solid;
}
.pics {
  float: left;
  margin: 5px 25px;
}
.pics img {
  padding-left: 15px;
  width: 160px;
  height: 100px;
}
.pics p {
  margin: 0;
  text-align: center;
  max-width: 160px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  padding: 1px 2px 3px 15px;
  font-size: 0.8em;
  cursor: pointer;
}
.pics p:hover {
  color: #127de7;
}
#pb-list2 ul {
  list-style-type: none;
  padding: 0 15px;
  text-align: left;
  float: left;
  width: 540px;
  box-sizing: border-box;
}
#pb-list2 ul li {
  background: none;
  padding: 0;
  margin: 0;
  border-top: none;
  line-height: 15px;
  padding: 8px 0 7px;
  font-size: 13px;
  clear: both;
}
.list_title {
  color: #3f3f3f;
  display: inline-block;
  max-width: 350px;
  overflow: hidden;
  padding: 0 10px;
  text-overflow: ellipsis;
  white-space: nowrap;
  line-height: 15px;
  vertical-align: middle;
  cursor: pointer;
}
.list_title:hover {
  color: #127de7;
}
#pb-list2 ul li b {
  float: right;
  padding: 5px 15px;
  font-weight: 100;
  display: block;
  height: 15px;
  overflow: hidden;
  font-family: Verdana;
  text-overflow: ellipsis;
}
.big {
  color: #df171e;
  padding: 4px 0;
  font-size: 16px;
  padding: 5px 10px;
  font-weight: bold;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
</style>



