<template>
  <div id="meeting-item">
    <div class="img">
      <img :src="item.coverImg">
    </div>
    <div class="item">
      <div class="title">{{item.title}}</div>
      <div class="time">时间：{{item.holdTime}}</div>
      <div class="location">地点：{{item.address}}</div>
      <div class="host">举办方：{{item.host}}</div>
      <div class="line"></div>
      <div class="message" title="view more">{{item.introduction}}</div>
      <div class="line"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "meeting-item",
  props: ["myMessage", "currentTab", "isRequested"],
  watch: {
    isRequested() {
      this.init();
    }
  },
  data() {
    return {
      item: {
        id: 1,
        coverImg: null, //封面图片地址
        title: "会议标题",
        holdTime: "",
        address: "123213",
        host: "123312", //举办方
        introduction: "个人数据挖掘和个人隐私保护，并非鱼与熊掌，可视分析的技术手段能够帮助我们保护个人隐私数据，避免后续的数据挖掘暴露隐私的同时，平衡数据质量发生的变化，减少对后续数据挖掘的影响。针对网络数据中的隐私保护问题，浙江大学、加州大学戴维斯分校和阿里云DataV团队，共同发表了最新的研究成果《GraphProtector" //会议介绍
      }
    };
  },
  methods: {
    init() {
      this.item = this.myMessage;
    }
  },
  activated() {
    this.init();
    // console.log(this.myMessage);
  },
  mounted() {
    this.init();
  }
};
</script>

<style lang="scss" scoped>
@mixin max {
  width: 100%;
  height: 100%;
}
#meeting-item {
  @include max;
  // border: 1px solid rgba(204, 204, 204, 0.575);
}
#meeting-item::after {
  content: "";
  display: block;
  clear: both;
}
.img {
  & {
    width: 350px;
    float: left;
    height: 250px;
    overflow: hidden;
  }
  img {
    width: 100%;
    height: 100%;
  }
}
.item {
  & {
    padding: 10px 20px;
    box-sizing: border-box;
    width: 440px;
    float: right;
    height: 250px;
    color: rgb(68, 68, 68);
    overflow: hidden;
  }
  .title {
    text-align: center;
    font-weight: 600;
    font-size: 20px;
    color: #d45b15;
    padding: 0;
    margin-bottom: 5px;
  }
  div {
    & {
      padding: 5px;
    }
  }
  .message {
    & {
      overflow: hidden;
      text-overflow: ellipsis;
      // white-space: nowrap;
      height: 80px;
      margin: 5px 0 10px 15px;
      display: -webkit-box;
      -webkit-line-clamp: 4; /*可指定任一行显示...效果*/
      -webkit-box-orient: vertical;
      letter-spacing: 2px;
      word-spacing: 2px;
      cursor: pointer;
    }
    &:hover {
      color: #e66f2a;
    }
  }
  .line {
    width: 93%;
    margin: 0 auto;
    content: "";
    padding: 2px;
    border-bottom: rgb(158, 158, 158) 1px dotted;
  }
}
</style>



