<template>
  <div id="meeting-dynamic">
    <!-- <ul class="title">
      <li class="menu_title">
        <slot name="title">
        </slot>
      </li>
      <li class="list_title" v-for=""></li>
    </ul> -->
    <div class="slide">
      <slide v-bind="$attrs"></slide>
    </div>
    <div class="normal-list">
        <ul>
          <li v-for="(item, index) in items" :key="index">
            <a class="list_title"> {{item.content}} </a>
          </li>
        </ul>
    </div>
  </div>
</template>

<script>
import Slide from "@/components/common/slide";

export default {
  name: "meeting-dynamic",
  props: ["myMessage", "isRequested", "currentTab"],
  data() {
    return {
      items: [
        {
          content: "大数据小哥哥小姐姐正在努力爬取数据呢..."
        },
        {
          content: "大数据小哥哥小姐姐正在努力爬取数据呢..."
        },
        {
          content: "大数据小哥哥小姐姐正在努力爬取数据呢..."
        },
        {
          content: "大数据小哥哥小姐姐正在努力爬取数据呢..."
        },
        {
          content: "大数据小哥哥小姐姐正在努力爬取数据呢..."
        },
        {
          content: "大数据小哥哥小姐姐正在努力爬取数据呢..."
        },
        {
          content: "大数据小哥哥小姐姐正在努力爬取数据呢..."
        },
        {
          content: "大数据小哥哥小姐姐正在努力爬取数据呢..."
        }
      ]
    };
  },
  methods: {
    init() {
      if (this.myMessage) {
        this.items = this.myMessage;
      }
    }
  },
  watch: {
    isRequested() {
      this.init();
    }
  },
  components: {
    Slide,
  }
};
</script>

<style lang="scss" scoped>
@mixin max {
  width: 100%;
  height: 100%;
}
#meeting-dynamic {
  @include max;
}
.slide {
  width: 350px;
  float: left;
  height: 250px;
  overflow: hidden;
}
.normal-list {
  width: 440px;
  float: right;
}
.normal-list ul {
  & {
    list-style-type: none;
    padding: 0 15px;
    text-align: left;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
  }
  li {
    & {
      padding: 6px 0 5px 10px;
      margin-left: 5px;
      font-size: 13px;
      clear: both;
      line-height: 20px;
    }
    .list_title {
      color: #3f3f3f;
      float: left;
      padding: 0;
      margin: 0;
      border-top: none;
      line-height: 15px;
      padding: 6px 0 5px;
      display: inline-block;
      width: 380px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      line-height: 15px;
      vertical-align: middle;
      cursor: pointer;
    }
    .list_title:hover {
      color: #127de7;
    }
  }
}
</style>


