<template>
  <div id="edit">
    <el-row class="warp">
      <el-col :span="24" class="warp-main" v-loading="loading" element-loading-text="拼命加载中">
        <!--工具条-->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
          <el-form :inline="true" :model="filters">
            <el-form-item>
              <el-input v-model="filters.name" placeholder="文章名" @keyup.enter.native="handleSearch"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="handleSearch">查询</el-button>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="showAddDialog">新增</el-button>
            </el-form-item>
          </el-form>
        </el-col>

        <!--列表-->
        <el-table :data="items" highlight-current-row @selection-change="selsChange" style="width: 100%; margin-bottom:10px;">
          <el-table-column type="selection" width="55"></el-table-column>
          <el-table-column type="index" width="60"></el-table-column>
          <el-table-column type="expand">
            <template slot-scope="props">
              <el-form label-position="left" inline class="demo-table-expand">
                <el-form-item label="[文章简介]">
                  <span>{{ props.row.content[0].content}}</span>
                </el-form-item>
              </el-form>
            </template>
          </el-table-column>
          <el-table-column prop="title" label="文章名" sortable></el-table-column>
          <el-table-column prop="source" label="来源" width="100" sortable></el-table-column>
          <el-table-column prop="postDate" label="日期" width="150" sortable></el-table-column>
          <el-table-column label="操作" width="200">
            <template slot-scope="scope">
              <el-button size="small" @click="showEditDialog(scope.$index,scope.row)">编辑</el-button>
              <el-button type="danger" @click="delBook(scope.$index,scope.row)" size="small">删除</el-button>
            </template>
          </el-table-column>
        </el-table>

        <!--工具条-->
        <el-col :span="24" class="toolbar">
          <el-button type="danger" @click="batchDeleteBook" :disabled="this.sels.length===0">批量删除</el-button>
          <el-pagination layout="prev, pager, next" background="" @current-change="handleCurrentChange" :page-size="10" :total="total" style="float:right;">
          </el-pagination>
        </el-col>

        <!-- <el-dialog title="编辑" :visible.sync="editFormVisible" :close-on-click-modal="false">
          <el-form :model="editForm" label-width="100px" :rules="editFormRules" ref="editForm">
            <el-form-item label="文章名" prop="name">
              <el-input v-model="editForm.name" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="来源" prop="author">
              <el-input v-model="editForm.author" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="日期">
              <el-date-picker type="date" placeholder="选择日期" v-model="editForm.publishAt"></el-date-picker>
            </el-form-item>
            <el-form-item label="简介" prop="description">
              <el-input type="textarea" v-model="editForm.description" :rows="8"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click.native="editFormVisible = false">取消</el-button>
            <el-button type="primary" @click.native="editSubmit">提交</el-button>
          </div>
        </el-dialog> -->

        <!--新增界面-->
        <!-- <el-dialog title="新增" :visible.sync="addFormVisible" :close-on-click-modal="false">
          <el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
            <el-form-item label="文章名" prop="name">
              <el-input v-model="addForm.name" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="作者" prop="author">
              <el-input v-model="addForm.author" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="日期">
              <el-date-picker type="date" placeholder="选择日期" v-model="addForm.publishAt"></el-date-picker>
            </el-form-item>
            <el-form-item label="简介" prop="description">
              <el-input type="textarea" v-model="addForm.description" :rows="8"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click.native="addFormVisible = false">取消</el-button>
            <el-button type="primary" @click.native="addSubmit" :loading="addLoading">提交</el-button>
          </div>
        </el-dialog> -->

      </el-col>
    </el-row>
  </div>
</template>
<script>
import API from "@/api/api_admin";
import Qs from "qs";

export default {
  name: "edit",
  props: ["currentTab", "isRequested"],
  data() {
    return {
      //搜索框内容
      filters: {
        name: ""
      },
      items: [
        {
          id: "",
          title:
            "广东省经济和信息化委关于开展2018年省战略性新兴产业骨干（培育）企业 （智能制造领域）申报及复审的通知",
          postDate: "2018-08-20",
          source: "广东省经济和信息化委员会",
          content:
            "广东省经济和信息化委员会：http://www.gdei.gov.cn/ywfl/jscx/201808/t20180820_130227"
        }
      ],
      total: 1, //文章总数
      page: 1,
      // limit: 10,
      loading: false,
      sels: [], //列表选中列
      //页面初始化相关数据
      //用于判断当前该发请求的url及post、get方式
      initFormRules: [
        {
          name: "用户列表",
          method: "",
          url: ""
        },
        {
          name: "近期活动",
          method: "",
          url: ""
        },
        {
          name: "协会活动",
          method: "",
          url: ""
        },
        {
          name: "新闻热点",
          method: "",
          url: ""
        },
        {
          name: "其他",
          method: "",
          url: ""
        },
        {
          name: "行业动态",
          method: "post",
          url: "/information/findInformationByPage"
        },
        {
          name: "资讯热点",
          method: "",
          url: ""
        },
        {
          name: "行业报告",
          method: "",
          url: ""
        },
        {
          name: "政策",
          method: "post",
          url: "/information/findPolicyByPage"
        },
        {
          name: "通知公告",
          method: "get",
          url: "/information/getNoticeList"
        },
        {
          name: "会员动态",
          method: "get",
          url: "/information/getCompanyDynamicsList"
        },
        {
          name: "协会会员",
          method: "get",
          url: "/introduction/getMemberList"
        },
        {
          name: "协会之窗",
          method: "",
          url: ""
        },
        {
          name: "教育培训",
          method: "",
          url: ""
        },
        {
          name: "企业新闻",
          method: "get",
          url: "/information/getCompanyNewsList"
        },
        {
          name: "品牌展厅",
          method: "",
          url: ""
        },
        {
          name: "会员动态",
          method: "get",
          url: "/information/getCompanyDynamicsList"
        },
        {
          name: "产品推荐",
          method: "",
          url: ""
        },
        {
          name: "产品新闻",
          method: "",
          url: ""
        },
        {
          name: "产品评测",
          method: "",
          url: ""
        },
        {
          name: "产品库",
          method: "",
          url: ""
        },
        {
          name: "技术研讨",
          method: "",
          url: ""
        },
        {
          name: "基础知识",
          method: "",
          url: ""
        },
        {
          name: "案例库",
          method: "",
          url: ""
        },
        {
          name: "专家库",
          method: "get",
          url: "/introduction/getExpertList"
        },
        {
          name: "专家智点",
          method: "get",
          url: "/expert/getExpertArtList"
        },
        {
          name: "高校",
          method: "get",
          url: "/introduction/getUniversityList"
        },
        {
          name: "展会动态",
          method: "",
          url: ""
        },
        {
          name: "未来展会",
          method: "",
          url: ""
        },
        {
          name: "展会回顾",
          method: "",
          url: ""
        },
        {
          name: "会议动态",
          method: "",
          url: ""
        },
        {
          name: "未来会议",
          method: "",
          url: ""
        },
        {
          name: "会议回顾",
          method: "",
          url: ""
        },
        {
          name: "广告1",
          method: "",
          url: ""
        },
        {
          name: "广告2",
          method: "",
          url: ""
        },
        {
          name: "广告3",
          method: "",
          url: ""
        }
      ]
      // //编辑相关数据
      // editFormVisible: false, //编辑界面是否显示
      // editFormRules: {
      //   name: [{ required: true, message: "请输入文章名", trigger: "blur" }],
      //   author: [{ required: true, message: "请输入来源", trigger: "blur" }],
      //   description: [
      //     { required: true, message: "请输入简介", trigger: "blur" }
      //   ]
      // },
      // editForm: {
      //   id: 0,
      //   name: "",
      //   author: "",
      //   publishAt: "",
      //   description: ""
      // },
      // //新增相关数据
      // addFormVisible: false, //新增界面是否显示
      // addLoading: false,
      // addFormRules: {
      //   name: [{ required: true, message: "请输入文章名", trigger: "blur" }],
      //   author: [{ required: true, message: "请输入来源", trigger: "blur" }],
      //   description: [
      //     { required: true, message: "请输入简介", trigger: "blur" }
      //   ]
      // },
      // addForm: {
      //   name: "",
      //   author: "",
      //   publishAt: "",
      //   description: ""
      // }
    };
  },
  methods: {
    //换页
    handleCurrentChange(val) {
      this.page = val;
      this.init();
    },
    //搜索
    handleSearch() {
      this.total = 0;
      this.page = 1;
      this.search();
    },
    search() {
      let that = this;
      // let params = {
      //   page: that.page,
      //   name: that.filters.name
      // };
      // var url = this.HOST + requestPath;
      //启动loading动画
      // that.loading = true;
      // API.findList(params)
      //   .then(
      //     function(result) {
      //       that.loading = false;
      //       if (result && result.books) {
      //         that.total = result.total;
      //         that.books = result.books;
      //       }
      //     },
      //     function(err) {
      //       that.loading = false;
      //       that.$message.error({
      //         showClose: true,
      //         message: err.toString(),
      //         duration: 2000
      //       });
      //     }
      //   )
      //   .catch(function(error) {
      //     that.loading = false;
      //     console.log(error);
      //     that.$message.error({
      //       showClose: true,
      //       message: "请求出现异常",
      //       duration: 2000
      //     });
      //   });
    },
    selsChange: function(sels) {
      this.sels = sels;
    },
    init() {
      console.log(this.currentTab);
      let that = this;

      let index = that.initFormRules.findIndex(
        menu => menu.name === that.currentTab
      );
      // console.log(index);
      let url = that.HOST + that.initFormRules[index].url;
      let method = that.initFormRules[index].method;
      // console.log(url, method);

      if (method == "post") {
        let params = Qs.stringify({
          pageNum: that.page //页数，默认为1
        });
        //启动loading动画
        that.loading = true;
        //发送post请求
        that
          .axios({
            method: "post",
            url: url,
            data: params
          })
          .then(response => {
            console.log(response.data);
            that.items = response.data.message.list;
            that.total = response.data.message.list.length;
            that.loading = false;
          })
          .catch(response => {
            console.log(response.data);
            that.loading = false;
            that.$message.error({
              showClose: true,
              message: "请求出现异常",
              duration: 2000
            });
          });
      } else if (method == "get") {
        let data = {
          pageNum: that.page //页数，默认为1
        };
        //启动loading动画
        that.loading = true;
        //发送get请求
        that.axios
          .get(url, {
            params: data
          })
          .then(response => {
            console.log(response.data);
            that.items = response.data.message.list;
            that.total = response.data.message.list.length;
            that.loading = false;
          })
          .catch(response => {
            console.log(response.data);
            that.loading = false;
            that.$message.error({
              showClose: true,
              message: "请求出现异常",
              duration: 2000
            });
          });
      } else {
        that.items = "";
        that.total = 0;
        that.$message.error({
          showClose: true,
          message: "数据异常",
          duration: 2000
        });
      }
    }
  },
  activated() {
    this.init();
  },
  watch: {
    isRequested() {
      this.init();
      console.log(this.isRequested);
    }
  }
};
</script>
<style lang="scss" scoped>
</style>


