<template>
  <div id="x_information">
    <div class="out_line">
      <div class="hot_search">
        <x_hotsearch></x_hotsearch>
      </div>
      <!--左边长列表-->
      <div class="box1-left">
        <div class="box2-left">
          <x_currentlocation :last_last_location="last_last_location" :current_where="current_place1" :last_location="last_location"></x_currentlocation>
        </div>
        <div class="box3-left">
          <x_singlesidebar :tab_three1="tabthree1[6]" :isRequested="isRequested"></x_singlesidebar>
        </div>
        <div class="box8-left">
          <x_singlesidebar :tab_three1="tabthree1[7]" v-bind:signal="signal[0].name" :isRequested="isRequested"></x_singlesidebar>  
        </div>
        <div class="box9-left">
          <x_singlesidebar :tab_three1="tabthree1[8]" v-bind:signal="signal[1].name" :isRequested="isRequested"></x_singlesidebar> 
        </div>
        <div class="box10-left">
          <x_singlesidebar :tab_three1="tabthree1[9]" :isRequested="isRequested"></x_singlesidebar>
        </div>
      </div>
      <!--右边长列表-->
      <div class="box4-right">
        <div class="box5-right">
          <x_singlesidebar v-bind:tab_three1="tabthree1[10]"  :isRequested="isRequested"></x_singlesidebar>  <!--这里可以优化一下，将数组合并为一个传输-->
        </div>
        <div class="box6-right">
          <x_tab_switch v-bind:tabt1="tabthree1" v-bind:signal="signal[0].name"></x_tab_switch>
        </div>
        <div class="box7-right">
          <x_tab_switch v-bind:tabt1="tabthree1" v-bind:signal="signal[1].name"></x_tab_switch>
        </div>
        <div class="box11-right">
          <x_singlesidebar v-bind:tab_three1="tabthree1[11]" :isRequested="isRequested"></x_singlesidebar>  <!--这里可以优化一下，将数组合并为一个传输-->
        </div>
      </div>

    </div>
  </div>

  </div>
</template>

<script>
import MHeader from "@/components/common/m-header";
import x_currentlocation from "./currentlocation.vue";
import x_singlesidebar from "./single_sidebar.vue";
import x_tab_switch from "./x_tab_switch.vue";
import x_hotsearch from "@/components/common/hotsearch.vue";
import x_hotsearch2 from "@/components/common/hot-search2.vue";

export default {
  name: "information",
  data() {
    return {
      last_location: "",
      last_last_location: "资讯",
      current_place1: "",
      tabthree1: [
        {
          name: "用户中心",
          belong: "用户",//属于哪个组件
          path: null, //点击跳转
          tocpn: "first", //子组件名
          message: null, //组件内容，对象数组
          isActive: true
        },
        {
          name: "订阅资讯",
          belong: "用户",//属于哪个组件
          path: null, //点击跳转
          tocpn: "second", //子组件名
          message: null, //组件内容，对象数组
          isActive: false
        },
        {
          name: "我的交易",
          belong: "用户",//属于哪个组件
          path: null, //点击跳转
          tocpn: "third", //子组件名
          message: null, //组件内容，对象数组
          isActive: false
        },
        {
          name: "近期活动",
          belong: "活动",//属于哪个组件
          path: null, //点击跳转
          tocpn: "first", //子组件名
          message: null, //组件内容，对象数组
          isActive: true
        },
        {
          name: "协会活动",
          belong: "活动",//属于哪个组件
          path: null, //点击跳转
          tocpn: "second", //子组件名
          message: null, //组件内容，对象数组
          isActive: false
        },
        {
          name: "其他",
          belong: "活动",//属于哪个组件
          path: null, //点击跳转
          tocpn: "third", //子组件名
          message: null, //组件内容，对象数组
          isActive: false
        },
        {
          name: "行业动态",
          belong: "资讯",//属于哪个组件
          path: null, //点击跳转
          tocpn: "", //子组件名
          message: null, //组件内容，对象数组
          isActive: false
        },
        {
          name: "资讯热点",
          belong: "资讯",//属于哪个组件
          path: null, //点击跳转
          tocpn: "", //子组件名
          message: null, //组件内容，对象数组
          isActive: false
        },
        {
          name: "政策",
          belong: "资讯",//属于哪个组件
          path: null, //点击跳转
          tocpn: "", //子组件名
          message: null, //组件内容，对象数组
          isActive: false
        },
        {
          name: "行业报告",
          belong: "资讯",//属于哪个组件
          path: null, //点击跳转
          tocpn: "", //子组件名
          message: null, //组件内容，对象数组
          isActive: false
        },
        
        {
          name: "相关热点",
          belong: "相关热点",//属于哪个组件
          path: null, //点击跳转
          tocpn: "", //子组件名
          message: null, //组件内容，对象数组
          isActive: false
        },
        {
          name: "广告招商",
          belong: "广告招商",//属于哪个组件
          path: null, //点击跳转
          tocpn: "", //子组件名
          message: null, //组件内容，对象数组
          isActive: false
        }
        
      ],
      signal:[
        {
          name:"用户"
        },
        {
          name:"活动"
        }
      ],
     /*  tabthree3:[
        {
          name: "行业动态"
        },
        {
          name: "资讯热点"
        },
        {
          name: "政策"
        },
        {
          name: "行业报告"
        }
      ], */
/*       tabsingle1: "相关热点",
      tabsingle2: "广告招商", */
      isRequested:false,  //这个没有什么大的意义现在
    };
  },
  methods:{
    init(){
      var vm = this;
      this.$http.get(this.HOST +"/common/getIndex").then((response)=>{
        console.log(response.data);
        //开始存数据
        this.tabthree1[6].message =response.data.message.news;
        //this.tabthree1[7].message =response.data.message.news;  //资讯热点
        this.tabthree1[8].message =response.data.message.policy;
        this.tabthree1[9].message =response.data.message.report;
        this.isRequested = true;
/*         console.log(document.location.href);
        var pathName = document.location.href;
        var index = pathName.substr(1).indexOf("/index");
        console.log(pathName.substr(1).indexOf("/index"));
        var result = pathName.substr(0, index + 1);
        console.log(result); */
      }).catch((response) =>{
        console.log(response.data);
      });
      
    }
  },
  components: {
    MHeader,
    x_currentlocation,
    x_singlesidebar,
    x_tab_switch,
    x_hotsearch,
    x_hotsearch2
  },
  activated(){
    this.init();
  }
};
</script>

<style lang="scss" scoped>
@mixin adv {
  margin: 5px auto;
  width: 1200px;
  height: 125px;
  //overflow: hidden;
}
@mixin w-box {
  margin: 5px auto;
  width: 1200px;
  height: 280px;
  overflow: hidden;
  margin-bottom: 18px;
}
@mixin i-box {
  box-shadow: 0 0 1px #bebebe;
  height: 100%;
  overflow: hidden;
}
/*右边小框重复样式*/
@mixin litter_one_tab_box {
  @include i-box;
  width: 390px;
  height: 300px;
  float: right;
  margin: 10px 0;
}

.hot_search {
  @include adv;
  width:auto;
  height: 40px;
  padding: 13px;
  //margin-left: 15px;
  background: rgba(255, 255, 255, 0.95);
}
.box1-left {
  width: 795px;
  float: left;
  height: 100%;
}
.box2-left {
  margin: 10px 0;
  padding: 13px;
  //box-shadow: 0 0 1px #bebebe;
  //margin-left: 15px;//好像不给这个左边无法出现box-shadow
  background: rgba(255, 255, 255, 0.95);
}
.out_line {
  @include adv;
  height: 100%;
  //box-shadow: 0 0 1px #bebebe;
}
/*左边四个小框样式*/
.box3-left,
.box8-left,
.box9-left,
.box10-left {
  @include i-box;
  height: 260px;
  margin: 10px 0;
  //margin-left: 15px;
  background: rgba(255, 255, 255, 0.95);
  padding: 0px 0 20px 0;
}
.box4-right {
  width: 390px;
  margin-top:10px;
  float: right;
  div{
    background: rgba(255, 255, 255, 0.95);
  }
}
.box5-right,
.box6-right,
.box7-right,
.box11-right {
  @include litter_one_tab_box;
  margin-right: 1px;//这里也是一定要移动一点margin
  background: rgba(255, 255, 255, 0.95);
}
.box5-right {
  margin-top: 0;
}
/* .box1-left>div:hover{
  box-shadow: -1px 1px 1px #bebebe;
}
.box2-left>div:hover{
  box-shadow: -1px 1px 1px #bebebe;
} */
</style>


