<template>
<div id="enterpriseRight">
  <div id="right">
      <div id="enterpriseHot">
        <div class="rightTitle">企业热点
          <div class="more">更多</div>
        </div>
        <div id="hotTitle">
          <router-link to="/enterprise" v-for="i in enterpriseHot" class="smallLink" :key="i">{{i}}<br></router-link>
        </div>
      </div>
      <div id="enterpriseMine">
        <div id="mineHeader">
          <div class="flexbox2">
            <span class="cursordefault" v-bind:class="{blue:(mine==1)}" @mouseover="changeMine(1,$event)"><i class="fa fa-user" aria-hidden="true"></i> 用户中心 </span>
            <span class="cursordefault" v-bind:class="{blue:(mine==2)}" @mouseover="changeMine(2,$event)"><i class="fa fa-file-text" aria-hidden="true"></i> 订阅资讯 </span>
            <span class="cursordefault" v-bind:class="{blue:(mine==3)}" @mouseover="changeMine(3,$event)"><i class="fa fa-check-square-o" aria-hidden="true"></i> 我的交易 </span>
          </div>
          <div v-if="mine==1" id="userCenter">userCenter</div>
          <div v-if="mine==2" id="takeNews">takeNews</div>
          <div v-if="mine==3" id="myDeal">myDeal</div>
        </div>
      </div>
      <div id="enterpriseOthers">
        <div id="otherHeader">
          <div class="flexbox2">
            <span class="cursordefault" v-bind:class="{blue:(others==1)}" @mouseover="changeOthers(1,$event)"><i class="fa fa-user" aria-hidden="true"></i> 近期活动 </span>
            <span class="cursordefault" v-bind:class="{blue:(others==2)}" @mouseover="changeOthers(2,$event)"><i class="fa fa-file-text" aria-hidden="true"></i> 协会活动 </span>
            <span class="cursordefault" v-bind:class="{blue:(others==3)}" @mouseover="changeOthers(3,$event)"><i class="fa fa-check-square-o" aria-hidden="true"></i> 其他 </span>
          </div>
          <div v-if="others==1" id="recentActive">recentActive</div>
          <div v-if="others==2" id="communityActive">communityActive</div>
          <div v-if="others==3" id="other">other</div>
        </div>
      </div>
      <div id="advertisePlace">
        广告招商
      </div>
  </div>
</div>
</template>

<script>
export default {
  name: 'enterpriseRight',
  data(){
    return {
      newsPics:["./static/PICS/timg5.jpg","./static/PICS/timg5.jpg"],
      enterpriseHot:["新松机器人科技天团亮相世界机器人大会",
        "2018年上边年广东工业机器人产量占全国1/5",
        "机器视觉公司扩博智能完成1100万美元融资",
        "2018世界机器人大会在北京举办",
        ],
      mine:1,
      others:1,
      rightShow:0,
    }
  },
  methods:{
    changeMine:function(n,event){
      this.mine=n;
      //event.target.style.color='red';
    },
    changeOthers:function(n,event){
      this.others=n;
    }
  }
}
</script>
<style lang="scss" scoped>
@mixin rightBorder{
  background:rgba(255,255,255,0.8);
  width:470px;
  margin:10px 0;
  min-height:150px;
  padding:5px;
}
.rightTitle{
  margin-bottom:5px;
}
.smallLink{
  height:30px;
  line-height:30px;
}
div#hotTitle{
  display:flex;
  flex-wrap:nowrap;
  flex-direction:column;
  justify-content:space-around;
  padding:0 5px;
  background:white;
}
.flexbox2{
    display:flex;
    flex-wrap:nowrap;
    justify-content:space-between;
    width:86%;
    position:relative;
    left:12%;
}
div#right{
  float:right;
  width:480px;
}
div#enterpriseHot,div#enterpriseMine,div#enterpriseOthers{
  @include rightBorder;
}
div#enterpriseMine span,div#enterpriseOthers span{
  margin-bottom:5px;
  width:33%;
}
</style>



