<template>
  <div id="slide" v-on:mouseover="stop()" v-on:mouseout="move()">
    <div class="slideshow">
      <transition-group tag="ul" name="image">
        <li v-for="(item, index) in items" v-show=" index === mark" v-if="index < 4" :key="index" @click="routerLink(item.id,currentTab)">
          <a href="#">
            <img :src='item.img'>
            <span class="product-title" :class="{'shot':bar_show}">{{item.title}}</span>
          </a>
        </li>
      </transition-group>
    </div>
    <transition name="show">
      <div class="bar" v-show=" bar_show == 1 ">
        <span v-for="(item, index) in items" :class="{'show': index === mark }" v-if="index < 4" @click="change(index)" :key="index"></span>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  name: "slide",
  data() {
    return {
      timer: null, //定时器
      mark: 0, //比对图片的索引
      bar_show: 0,
      items: [
        {
          imgArray: "./static/PICS/pic1.jpg",
          title: "120家世界500强的中国企业 一成入局机器人",
          id: ""
        },
        {
          imgArray: "./static/PICS/pic2.png",
          title: "中国已诞生14只AI“独角兽” 总估值达405亿美元",
          id: ""
        },
        {
          imgArray: "./static/PICS/pic3.png",
          title: "日本开发采血机器人 利用红外探头可准确找到血管",
          id: ""
        },
        {
          imgArray: "./static/PICS/pic4.jpg",
          title: "优衣库启用第一个“机器人仓库” 用机器人取代了90%员工",
          id: ""
        }
      ]
    };
  },
  props: ["myMessage", "isRequested", "currentTab"],

  methods: {
    //轮播
    autoPlay() {
      this.mark++;
      if (this.mark === 4) {
        //当遍历到最后一张图片置零
        this.mark = 0;
      }
    },
    play() {
      this.timer = setInterval(this.autoPlay, 2500);
    },
    change(i) {
      this.mark = i;
    },
    stop() {
      clearInterval(this.timer);
      this.bar_show = 1;
    },
    move() {
      this.timer = setInterval(this.autoPlay, 2500);
      this.bar_show = 0;
    },
    //init
    init() {
      if (this.myMessage) {
        this.items = this.myMessage;
        this.myMessage.forEach(a => {
          if (a.img == null) {
            a.img =
              "http://bpic.588ku.com/element_origin_min_pic/18/06/10/2a53a771f6f89141ac2a513ae34d9cac.jpg";
          }
        });
        // console.log(this.items);
      }
    },
    routerLink(messageId, item) {
      let data = JSON.stringify(item);
      if (this.myMessage) {
        // console.log(messageId);

        let url = "/news/industry_dynamic_text";
        if (this.currentTab.name == "资讯") {
          url = "/news/industry_dynamic_text";
        }
        if (this.currentTab.name == "协会") {
          url = "/association/industry_dynamic_text";
        }
        if (this.currentTab.name == "产品") {
          url = "/product/industry_dynamic_text";
        }
        if (this.currentTab.name == "人才") {
          url = "/talent/talent_experience_text";
        }
        if (this.currentTab.list == "政策") {
          url = "/policy/policy_text";
        }
        this.$router.push({
          path: url,
          query: { messageId: messageId, item: data }
        });
      }
    }
  },
  created() {
    this.play();
  },
  activated() {
    this.init();
    // console.log(this.myMessage);
  },
  watch: {
    isRequested() {
      this.init();
    }
  },
  components: {}
};
</script>

<style lang="scss" scoped>
* {
  margin: 0;
  padding: 0;
  list-style: none;
}
@mixin whole {
  width: 100%;
  height: 100%;
}
#slide {
  @include whole;
  margin: 0 auto;
  position: relative;
  overflow: hidden;
}
.slideshow {
  @include whole;
}
li {
  position: absolute;
  @include whole;
}
li a {
  @include whole;
  margin: 0;
  padding: 0;
  position: absolute;
}
img {
  @include whole;
}
.product-title {
  display: block;
  position: absolute;
  top: auto;
  right: 0;
  bottom: 0;
  left: 0;
  width: auto;
  padding: 10px 15px 11px;
  // padding-right: 100px;
  height: 20px;
  line-height: 20px;
  background: rgba(0, 0, 0, 0.35);
  color: #fff;
  font-size: 14px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.shot {
  padding-right: 25%;
}
.paddingchosen {
  padding-right: 100px;
}
.bar {
  position: absolute;
  width: 100%;
  box-sizing: border-box;
  bottom: 10px;
  padding: 0 15px 0 0;
  z-index: 10;
  text-align: right;
}
.bar span {
  width: 13px;
  height: 13px;
  border-radius: 5px;
  border: 1px solid rgba(0, 0, 0, 0.685);
  background: white;
  display: inline-block;
  margin-right: 5px;
  cursor: pointer;
}
.show {
  background: red !important;
}
// 过渡效果
.image-enter-active {
  transform: translateX(0);
  transition: all 1.5s ease;
}
.image-leave-active {
  transform: translateX(-100%);
  transition: all 1.5s ease;
}
.image-enter {
  transform: translateX(100%);
}
.image-leave {
  transform: translateX(0);
}
.show-enter-active,
.show-leave-active {
  transition: all 0.5s;
}
.show-enter,
.show-leave-to {
  margin-left: 100px;
}
.show-enter-to,
.show-leave {
  margin-left: 0px;
}
</style>


