<template>
  <div id="enterpriseHome">
    <div id="left">
      <div id="location">
        当前位置：<router-link to="/home" class="selectable">服务平台</router-link> > <span class=blue>企业</span>
      </div>
      <div id="enterpriseNews">
        <div>
            <i class="fa fa-align-justify" aria-hidden="true"></i> 企业新闻
            <div class="more">更多</div>
        </div>
        <div id="enterpriseNewsPics" v-if="message">
          <div id="picsSelect" @click.stop>
            <button v-for="(i,index) in message.companyNews" class="selectButton selectable" v-bind:class="{activeButton:play==0?play1==index:play2==index}" @click.stop="nextPic(index)">{{index + 1}}</button>
          </div>
          <transition name="slide1" enter-active-class="slideInRight" leave-active-class="slideOutLeft">
            <div v-if="play==0" class="newsPicFrame" @click.stop="routerLinkName('enterpriseNews',message.companyNews[play1].id,'id')">
              <img class="newsPic selectable" v-bind:src="newsPics[play1]">
              <br>
              <div class="newsText">这是一段新闻介绍，北京申奥成功啦！这是一段新闻介绍，北京申奥成功啦!这是一段新闻介绍，北京申奥成功啦!这是一段新闻介绍，北京申奥成功啦!</div>
            </div>
          </transition>
          <transition name="slide2" enter-active-class="slideInRight" leave-active-class="slideOutLeft">
            <div v-if="play==1" class="newsPicFrame" @click.stop="routerLinkName('enterpriseNews',message.companyNews[play2].id,'id')">
              <img class="newsPic selectable" v-bind:src="newsPics[play2]">
              <br>
              <div class="newsText">这是一段新闻介绍，北京申奥成功啦！这是一段新闻介绍，北京申奥成功啦!这是一段新闻介绍，北京申奥成功啦!这是一段新闻介绍，北京申奥成功啦!</div>
            </div>
          </transition>
        </div>
      </div>
      <div id="enterpriseDisplay">
        <div @click="routerLink('/enterprise/enterpriseHall')" class="more">更多</div>
        <div class="smallTitle">
          <i class="fa fa-picture-o" aria-hidden="true"></i> 品牌展厅
        </div>
        <div class="flexbox" v-if="message">
          <img v-for="(i,index) in message.companyBrand" v-bind:src="i.logo" class="displayPic selectable" @click="routerLinkName('enterpriseDisplay',message.companyBrand[index].id,'id')">
        </div>
      </div>
      <div id="memberState" v-if="message">
        <div class="smallTitle">
            <i class="fa fa-users" aria-hidden="true"></i> 会员动态
            <div class="more">更多</div>
        </div>
        <div id="memberBox" class="flexbox">
            <div class="state" v-for="(i,index) in message.companyDynamics">
                {{i.title}}
            </div>
            <div class="state" v-if="(message.companyDynamics.lenght) % 2!=0">
            </div>
        </div>
      </div>
      <br>
    </div>
    <enterpriseRight></enterpriseRight>
  </div>
</template>

<script>
import axios from "axios";
import enterpriseRight from "@/components/enterprise/enterpriseRight";
export default {
    name: 'enterpriseHome',
    data(){
        return {
            message:undefined,
            theParams:undefined,
            newsPics:["./static/PICS/timg.jpg",
                "./static/PICS/timg2.jpg",
                "./static/PICS/timg3.jpg",
                "./static/PICS/timg4.jpg",
                "./static/PICS/timg5.jpg",
                "./static/PICS/timg.jpg",
                "./static/PICS/timg2.jpg",
                "./static/PICS/timg3.jpg",
                "./static/PICS/timg4.jpg",],
            enterpriseHot:["新松机器人科技天团亮相世界机器人大会",
                "2018年上边年广东工业机器人产量占全国1/5",
                "机器视觉公司扩博智能完成1100万美元融资",
                "2018世界机器人大会在北京举办"
                ],
            getBrand:undefined,
            getCompanyNews:undefined,
            play:0,
            play1:0,
            play2:1,
        }
    },
    components:{
        enterpriseRight
    }
    ,
    methods:{
        routerLink:function(path){
            this.$router.push(path);
        },
        routerLinkName:function(theName,paramValue,paramName){
            this.params=this.params || new Object();
            this.params[paramName]=paramValue;
            console.log("params",this.params)
            this.$router.push({name:theName,params:this.params})
        },
        nextPic:function(num){  //num表示要播放的，如果没有就表示下一张
            clearTimeout(this.newsInterval);
            this.newsInterval=setTimeout(this.nextPic,2333);
            if (this.play==0){
                if (num==this.play1) return false;
                this.play2=num!==undefined?num:(this.play1+1) % this.message.companyNews.length;
            }
            else{
                if (num==this.play2) return false;
                this.play1=num!==undefined?num:(this.play2+1) % this.message.companyNews.length;
            }
            this.play=(this.play + 1) % 2;
        },
    },
    created:function(){
        /*
        this.axios.get(this.HOST + '/company/getBrand')
        .then((res)=>{
            this.getBrand=res;
            console.log(this.getBrand);
        });
        this.axios.get(this.HOST + '/company/getCompanyNews')
        .then((res)=>{
            this.getCompanyNews=res.message;
            console.log(res.result)
        })
        */
        this.axios.get(this.HOST + 'common/getIndex')
        .then((res)=>{
          this.message=res.data.message;
          console.log("message",this.message);
        });
        setTimeout(this.nextPic,1551);
    },
    mounted:function(){
        console.log(this.x);
    }
}
</script>
<style lang="scss" scoped>
*{
    padding:0;
    margin:0;
}
@mixin leftBorder{
    padding:5px;
    width:700px;
    margin-bottom:10px;
    background:rgba(255,255,255,0.8);
}
@mixin picBig{
    width:700px;
    height:395px;
}
.flexbox{
    display:flex;
    flex-wrap:wrap;
    justify-content:space-around;
    background:white;
}
.smallTitle{
    margin-bottom:5px;
}
div#enterpriseNews,div#enterpriseDisplay,div#memberState{
    @include leftBorder;
    position:relative;
}
div#enterpriseNews{
    height:420px;
    position:relative;
    overflow:hidden;
    background:white;
}
div#picsSelect{
    position:absolute;
    left:50%;
    transform:translateX(-50%);
    bottom:35px;
    z-index:999;
}
button.selectButton{
    background:white;
    color:black;
    margin:0px 4px;
    border:0;
    width:1.2rem;
    height:1.2rem;
    font-size:0.8rem;
    border-radius:3px;
    outline:none;
    opacity:0.88;
}
button.activeButton{
    transition:666ms;
    background:black;
    color:white;
}
div#enterpriseNewsPics{
    @include picBig;
    overflow:hidden;
    white-space:nowrap;
    position:absolute;
    left:5px;
    bottom:5px;
}
div.newsPicFrame{
    position:absolute;
    bottom:0px;
    left:0px;
    @include picBig;
    overflow:hidden;
}
img.newsPic{
    @include picBig;
}
div.newsText{
    position:absolute;
    width:690px;
    padding-left:10px;
    bottom:0px;
    color:white;
    background:rgba(0,0,0,0.66);
    height:28px;
    line-height:28px;
}
img.displayPic{
    width:128px;
    height:72px;
    margin:20px 10px;
}
div#memberBox{
    background:rgba(0,0,0,0);
    justify-content: space-around;
}
.state{
    background:white;
    width:335px;
    padding:5px;
    margin-bottom:5px;
    cursor:pointer;
}
.state:hover{
    color:#127de7;
}
.slideInRight{
    animation-duration: 500ms;
}
.slideOutLeft{
    animation-duration: 500ms;
}
</style>



