<template>
  <div id="Title">
    <!-- <m-header ref="c1"></m-header> -->
    <h1>{{title}}</h1>
    <div class="Title_bottom">
      <div class="x_time">{{time}}</div>
      <div class="comming_2">来源 :</div>
      <div class="comming_1"> {{source}}</div>
      <div class="x_coming_from_2">阅读量：</div>
      <div class="x_coming_from_1">{{reading_amount}}</div>
      <div class="x_sharing">分享</div>
      <div class="x_erroring">纠错</div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //title: "直击世界机器人大会：人机共融已是大势所趋",
      coming: ""
    };
  },
  props: {
    title: {
      type: String,
      required: true
    },

    time: {
      type: String,
      required: true
    },
    reading_amount: {
      required: true
    },
    source:{
      required: true
    }
  }
};
</script>

<style lang="scss" scoped>
h1{
  color: rgb(51, 51, 51);
  font: bold 24px/30px "Microsoft YaHei", 微软雅黑, SimHe, 黑体;
  padding:0 13px;
  padding-bottom: 14px;
}
.x_time {
  & {
    display: inline-block;
    margin: 5px;
    padding-right: 7px;
    color: #999;
    height: 17px;
    line-height: 17px;
    overflow: hidden;
  }
  float: left;
}
.Title_bottom{
      padding: 15px 0 45px 0;
      float: left;
      overflow: hidden;
      height: 13px;
      font: 14px "瀹嬩綋";
      color: #444;
}
.comming_1,
.comming_2,
.x_coming_from_1,
.x_coming_from_2,
.x_sharing,
.x_erroring {
  display: inline-block;
  margin: 5px 3px;
  //padding: 0 10px;
  //padding-right: 2px;
  color: #999;
  height: 17px;
  line-height: 17px;
  overflow: hidden;
}
.comming_1,
.x_coming_from_1,
.x_sharing,
.x_erroring{
    border-right: 1px solid #ccc;
    padding:0 8px 0 0 ;
}
.x_erroring,
.x_sharing {
  margin: 5px 10px;
  float: right;
}
#Title {
  border-bottom: 1px dashed #ccc;
  width: 100%;
  height: 100%;
}
</style>


