<template>
  <div id="exhibition-default">
    <div class="left_block">
      <!-- 位置路径 -->
      <div class="box1">
        <div class="curr_position">
          当前位置：
          <a @click="selectItem('/home')">服务平台</a>
          <span>></span>
          <a @click="selectItem('/exhibition')">展会</a>
        </div>
      </div>
      <!-- 展会动态 -->
      <div class="box2">
        <ul class="title">
          <li class="list_title">
            <a class="active" style="margin-left:15px;">展会动态</a>
          </li>
          <span class="more" @click="selectItem('')">-更多></span>
        </ul>
        <div style="height:270px;width:100%;">
          <keep-alive>
            <meeting-dynamic></meeting-dynamic>
          </keep-alive>
        </div>
      </div>
      <!-- 即将举办 -->
      <div class="box3">
        <ul class="title">
          <li class="list_title">
            <a class="active" style="margin-left:15px;">即将举办</a>
          </li>
          <span class="more" @click="selectItem('')">-更多></span>
        </ul>
        <div style="width:100%;">
          <meeting-item style="margin-bottom:20px;" v-for="(item,index) in tabMessage[1].slice((currentPage[0]-1)*pageSize[0],currentPage[0]*pageSize[0])" :key="item.id" :my-message="tabMessage[1][index+(currentPage[0]-1)*pageSize[0]]" :current-tab="currentTab[1]"></meeting-item>
          <el-pagination background layout="prev, pager, next" :total="parseInt(total[0])" :page-size="pageSize[0]" :current-page.sync="currentPage[0]">
          </el-pagination>
        </div>
      </div>
      <!-- 展会回顾 -->
      <div class="box3">
        <ul class="title">
          <li class="list_title">
            <a class="active" style="margin-left:15px;">展会回顾</a>
          </li>
          <span class="more" @click="selectItem()">-更多></span>
        </ul>
        <div style="width:100%;">
          <meeting-item style="margin-bottom:20px;" v-for="(item,index) in tabMessage[2].slice((currentPage[1]-1)*pageSize[1],currentPage[1]*pageSize[1])" :key="item.id" :my-message="tabMessage[2][index+(currentPage[1]-1)*pageSize[1]]" :current-tab="currentTab[2]"></meeting-item>
          <el-pagination background layout="prev, pager, next" :total="parseInt(total[1])" :page-size="pageSize[1]" :current-page.sync="currentPage[1]" @current-change="currentChange()">
          </el-pagination>
        </div>
      </div>
    </div>
    <div class="right_block">
      <!-- 精彩展会   -->
      <div class="box4">
        <ul class="title">
          <li class="list_title">
            <a style="margin-left:15px;" class="active">精彩展会</a>
          </li>
        </ul>
        <div style="height:270px;width:100%;">
          <keep-alive>
            <!-- <advertise-list></advertise-list> -->
          </keep-alive>
        </div>
      </div>
      <!-- 用户 -->
      <div class="box4">
        <ul class="title" style="margin-left:15px;">
          <li class="list_title" v-for="(tab,index) in tabs" v-if="tab.belong === '用户'" :key="tab.name" @mouseover="tabLink(index,tab.tocpn)">
            <a :class="{active:tab.isActive}">{{tab.name}}</a>
          </li>
        </ul>
        <div style="height:270px;width:100%;">
          <keep-alive>
            <pb-list :is="currentTab[4].cpn"></pb-list>
          </keep-alive>
        </div>
      </div>
      <!-- 活动 -->
      <div class="box4">
        <ul class="title" style="margin-left:15px;">
          <li class="list_title" v-for="(tab,index) in tabs" v-if="tab.belong === '活动'" :key="tab.name" @mouseover="tabLink(index,tab.tocpn)">
            <a :class="{active:tab.isActive}">{{tab.name}}</a>
          </li>
        </ul>
        <div style="height:270px;width:100%;">
          <keep-alive>
            <pb-list :is="currentTab[5].cpn"></pb-list>
          </keep-alive>
        </div>
      </div>
      <!-- 广告招收 -->
      <div class="box4">
        <ul class="title">
          <li class="list_title">
            <a style="margin-left:15px;" class="active">广告招收栏目</a>
          </li>
        </ul>
        <div style="height:270px;width:100%;">
          <keep-alive>
            <!-- <advertise-list></advertise-list> -->
          </keep-alive>
        </div>
      </div>
    </div>
  </div>
</template>

