          }
        ],
        "",
        ""
      ], //动态。即将举办、已经举办
      //标志性内容，传给子组件做导航判断请求接口判断
      currentTab: [
        {
          name: "展会",
          list: "展会动态",
          cpn: "meeting-dynamic"
        },
        {
          name: "展会",
          list: "即将举办",
          cpn: "meeting-item"
        },
        {
          name: "展会",
          list: "展会回顾",
          cpn: "meeting-item"
        },
        {
          name: "",
          list: "精彩展会",
          cpn: ""
        },
        {
          name: "用户",
          list: "",
          cpn: "normal-list",
          newsUrl: ""
        },
        {
          name: "活动",
          list: "",
          cpn: "normal-list",
          newsUrl: ""
        }
      ],
      tabs: [
        {
          name: "用户中心",
          belong: "用户", //归属的小标题
          path: null, //点击跳转
          tocpn: "", //子组件名
          content: null, //组件内容，对象数组
          isActive: true
        },
        {
          name: "订阅资讯",
          belong: "用户", //归属的小标题
          path: null, //点击跳转
          tocpn: "normal-list", //子组件名
