import Vue from 'vue'
import Router from 'vue-router'
import axios from 'axios'
import VueAxios from 'vue-axios'
import Home from '@/components/home/home'
import News from '@/components/news/news'
import x_information from '@/components/news/information'
import industry_dynamic_text from '@/components/news/industry_dynamic_text'
import industry_dynamic from '@/components/news/industry_dynamic'
import VipList from '@/components/talent/talent-list'
import Policy from '@/components/policy/policy'
import Public from '@/components/public/public'
import Product from '@/components/product/product'
import ProductHome from '@/components/product/product-home'
import Association from '@/components/association/association'
import Enterprise from '@/components/enterprise/enterprise'
import EnterpriseHome from '@/components/enterprise/enterpriseHome'
import EnterpriseDisplay from '@/components/enterprise/enterpriseDisplay'
import EnterpriseHall from '@/components/enterprise/enterpriseHall'
import EnterpriseNews from '@/components/enterprise/enterpriseNews'
import Exhibition from '@/components/exhibition/exhibition'
import ExhibitionDefault from '@/components/exhibition/exhibition-default'
import Tech from '@/components/tech/tech'
import Talent from '@/components/talent/talent'
import TalentJob from '@/components/talent/talent-job'
import TalentJDefault from '@/components/talent/talent-default'
import TalentApply from '@/components/talent/talent-apply'
import TalentList from '@/components/talent/talent-list'
import TalentDetail from '@/components/talent/talent-detail'
import TalentInformation from '@/components/talent/talent-information'
import About from '@/components/about/about'
import Meeting from '@/components/meeting/meeting'
import MeetingDefault from '@/components/meeting/meeting-default'
import policy_text from '@/components/news/policy_text'
import x_policy from '@/components/news/policy'
import Questionnare from '@/components/questionnare/questionnare'
import Admin from '@/components/admin/admin'



Vue.use(Router)
Vue.use(VueAxios, axios)

export default new Router({
  routes: [
    //首页
    {
      path: '/home',
      name: 'home',
      component: Home,
      // alias:'/home',
    },
    //资讯
    {
      path: '/news',
      name: 'news',
      component: News,
      // alias:'/news',
      children: [
        {
          path: '/',
          name: 'x_information',
          component: x_information,
        },//资讯主页
        {
          path: 'vip-list',
          name: 'vip-list',
          component: VipList,
        },//协会会员
        {
          path: 'industry_dynamic_text',
          name: 'industry_dynamic_text',
          component: industry_dynamic_text,
          // alias:'/industry_dynamic_text',
        },//行业动态正文      
        {
          path: 'industry_dynamic',
          name: 'industry_dynamic',
          component: industry_dynamic,
          // alias:'/industry_dynamic',
        },//行业动态
      ]
    },
    //政策
    {
      path: '/policy',
      name: 'policy',
      component: Policy,
      // alias:'/policy',
      children: [
        {
          path: '/',
          name: 'x_policy',
          component: x_policy,
        },
        {
          path: 'policy_text',
          name: 'policy_text',
          component: policy_text,
          // alias:'/industry_dynamic_text',
        },//政策正文 
        {
          path: 'industry_dynamic',
          name: 'industry_dynamic',
          component: industry_dynamic,
          // alias:'/industry_dynamic',
        },//政策列表  
        {
          path: 'industry_dynamic_text',
          name: 'industry_dynamic_text',
          component: industry_dynamic_text,
          // alias:'/industry_dynamic_text',
        },//行业动态正文             
      ]
    },
    //协会
    {
      path: '/association',
      name: 'association',
      component: Association,
      // alias:'/association',
      children: [
        {
          path: '/',
          name: 'x_information',
          component: x_information,
        },//协会主页
        {
          path: 'vip-list',
          name: 'vip-list',
          component: VipList,
        },//协会会员
        {
          path: 'vip-detail',
          name: 'vip-detail',
          component: TalentDetail
        },
        {
          path: 'industry_dynamic_text',
          name: 'industry_dynamic_text',
          component: industry_dynamic_text,
          // alias:'/industry_dynamic_text',
        },//动态正文      
        {
          path: 'industry_dynamic',
          name: 'industry_dynamic',
          component: industry_dynamic,
          // alias:'/industry_dynamic',
        },//动态
      ]
    },
    //企业
    {
      path: '/enterprise',
      name: 'enterprise',
      component: Enterprise,
      // alias:'/enterprise',
      children: [
        {
          path: '/',
          name: 'enterpriseHome',
          component: EnterpriseHome
        },
        {
          path: 'enterpriseDisplay/:id',
          name: 'enterpriseDisplay',
          component: EnterpriseDisplay
        },
        {
          path: 'enterpriseHall',
          name: 'enterpriseHall',
          component: EnterpriseHall
        },
        {
          path: 'industry_dynamic_text',
          name: 'industry_dynamic_text',
          component: industry_dynamic_text,
          // alias:'/industry_dynamic_text',
        },//正文      
        {
          path: 'industry_dynamic',
          name: 'industry_dynamic',
          component: industry_dynamic,
          // alias:'/industry_dynamic',
        },//列表
        {
          path:'enterpriseNews/:id',
          name:'enterpriseNews',
          component: EnterpriseNews,
        }
      ]
    },
    //产品
    {
      path: '/product',
      name: 'product',
      component: Product,
      children: [
        {
          path: '/',
          name: 'productHome',
          component: ProductHome
        },
        {
          path: 'industry_dynamic_text',
          name: 'industry_dynamic_text',
          component: industry_dynamic_text,
          // alias:'/industry_dynamic_text',
        },//动态正文      
        {
          path: 'industry_dynamic',
          name: 'industry_dynamic',
          component: industry_dynamic,
          // alias:'/industry_dynamic',
        },//动态
        {
          path:'product-list',
          name:'product-list',
          component: TalentList
        },//产品列表
        {
          path:'product-detail',
          name:'product-detail',
          component: TalentDetail
        }//产品详细
      ]
    },
    //技术
    {
      path: '/tech',
      name: 'tech',
      component: Tech,
      children:[
        {
          path: '/',
          name: 'productHome',
          component: ProductHome
        },
        {
          path: 'industry_dynamic_text',
          name: 'industry_dynamic_text',
          component: industry_dynamic_text,
          // alias:'/industry_dynamic_text',
        },//动态正文      
        {
          path: 'industry_dynamic',
          name: 'industry_dynamic',
          component: industry_dynamic,
          // alias:'/industry_dynamic',
        },//动态
      ]
    },
    //人才
    {
      path: '/talent',
      name: 'talent',
      component: Talent,
      children: [
        {
          path: '/',
          name: 'talent-default',
          component: TalentJDefault,
        },//人才主页
        {
          path: 'industry_dynamic_text',
          name: 'industry_dynamic_text',
          component: industry_dynamic_text,
          // alias:'/industry_dynamic_text',
        },//动态正文      
        {
          path: 'industry_dynamic',
          name: 'industry_dynamic',
          component: industry_dynamic,
          // alias:'/industry_dynamic',
        },//动态
        {
          path: 'talent-job',
          name: 'talent-job',
          component: TalentJob,
        },//最新招聘
        {
          path: 'talent-apply',
          name: 'talent-apply',
          component: TalentApply,
        },//职位招聘
        {
          path: 'talent-information',
          name: 'talent-information',
          component: TalentInformation,
        },//个体详细信息
        {
          path: 'talent-list',
          name: 'talent-list',
          component: TalentList,
        },//专家库、高校
        {
          path: 'talent-detail',
          name: 'talent-detail',
          component: TalentDetail
        },
        {
          path: 'talent_experience_text',
          name: 'talent_experience_text',
          component: industry_dynamic_text,
          // alias:'/industry_dynamic_text',
        },//专家智点正文     
        {
          path: 'talent_experience',
          name: 'talent_experience',
          component: industry_dynamic,
          // alias:'/industry_dynamic',
        },//专家智点 
      ]
    },
    //展会
    {
      path: '/exhibition',
      name: 'exhibiton',
      component: Exhibition,
      children: [
        {
          path: '/',
          name: 'exhibition-default',
          component: ExhibitionDefault
        },
        {
          path:'questionnare',
          name:'questionnare',
          component:Questionnare
        }
      ]
    },
    //会议
    {
      path: '/meeting',
      name: 'meeting',
      component: Meeting,
      children: [
        {
          path: '/',
          name: 'meeting-default',
          component: MeetingDefault
        }
      ]
    },
    //公众号
    {
      path: '/public',
      name: 'poublic',
      component: Public,
    },
    //关于我们
    {
      path: '/about',
      name: 'about',
      component: About,
    },
    //管理员
    {
      path: '/admin',
      name: 'admin',
      component:Admin,
      // children:[
      //   {
      //     path: 'user-list',
      //     name: 'user-list',
      //     component:UserList,
      //   },//用户列表
      // ]
    },
    {
      path: '/',
      // component: Home,
      redirect: Home,
    }
  ],
  // mode: "history"
})



