<template>
  <div id="pen-list">
    <div class="ul_list">
      <p v-for="(item,index) in items" class="perpon-name" @click="routerLink(index,currentTab)">
        {{item.name}}
      </p>
    </div>
  </div>
</template>

<script>
export default {
  name: "pen-list",
  data() {
    return {
      items: [
        { name: "张三丰" },
        { name: "李四" },
        { name: "斯柯达" },
        { name: "阿尔法" },
        { name: "杰克逊" },
        { name: "史密斯" },
        { name: "阿达瓦" },
        { name: "阿凡达" },
        { name: "闪电侠" },
        { name: "小飞侠" },
        { name: "helloworld" },
        { name: "奥特曼" },
        { name: "abc" },
        { name: "bcs" },
        { name: "bcs" },
        { name: "abc" },
        { name: "bcs" },
        { name: "abc" }
      ],
      init_data: {
        name: "",
        belong: "",
        catalog: ""
      }
    };
  },
  props: ["myMessage", "isRequested", "currentTab"],
  watch: {
    isRequested() {
      this.init();
    }
  },
  methods: {
    init() {
      if (this.myMessage) {
        this.items = this.myMessage;
      }
    },
    routerLink(index, data) {
      let id = this.items[index].id;
      let address = "/talent/talent-detail";
      let item =JSON.stringify(data); 
      this.$router.push({ path: address, query: { id: id, data: item } });
    }
  },
  mounted() {
    this.init();
  },
  activated() {
    this.init();
  }
};
</script>

<style lang="scss" scoped>
@mixin max {
  width: 100%;
  height: 100%;
}
#pen-list {
  @include max;
}
.ul_list {
  width: 100%;
  float: right;
  box-sizing: border-box;
}
.perpon-name {
  display: inline-block;
  width: 160px;
  margin: 0 auto;
  padding: 4px 10px;
  text-align: center;
  color: #7b7b7b;
  cursor: pointer;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.perpon-name:hover {
  color: #2e2d2d;
}
</style>



