<template>
  <div id="association">
    <keep-alive>
      <router-view @select="routerLink"></router-view>
    </keep-alive>
  </div>
</template>

<script>
export default {
  name: "association",
  data() {
    return {};
  },
  methods: {
    routerLink(path) {
      this.$router.push(path); //路由跳转
    }
  },
  components: {}
};
</script>

<style lang="scss" scoped>
</style>




